import React, { Component } from "react";

import "../styles/Competetor.css";

import logo from "../assets/64px-HP_logo_2012.svg.png";
// import { Link } from "react-router-dom";
// import { thisExpression } from "@babel/types";

export default class CompetetorsPage extends Component {
  state = {
    tickerId: this.props.tickerId,
    ticker_name: this.props.ticker_name,
    ticker_sector: this.props.ticker_sector,
    ticker_competetor: this.props.ticker_competetor,
    history: this.props.history
  };
  componentDidUpdate(nextProps) {
    if (nextProps.ticker_id !== this.state.ticker_id) {
      console.log("from comp didupdate", nextProps.ticker_id);
    }
  }
  render() {
    console.log("from competetor page", this.state.ticker_competetor);
    return (
      <div>
        <div className="competetor-page-container">
          {this.state.ticker_competetor.TopCompetetors.map(data => (
            <div className="main-card-contain">
              <div className="flex-company-name">
                <div className="company-logo">
                  <img className="logo-image" src={logo} />
                </div>
                <div className="company-name">
                  <p className="com-name" id="jest-com-name">
                    {data.tickerName_new}
                  </p>

                  <p className="company-tickers" id="jest-ind-name">
                    {data.industry_name}
                  </p>
                </div>
              </div>

              <div className="flex-company-values">
                <div className="company-stats">
                  <p className="comp-stock-price" id="jest-stock-p">
                    {data.current_share_price} USD
                  </p>
                </div>
                <div className="company-unfollow">
                  <a href={"http://localhost:3000/stocks/" + data.ticker_id}>
                    <button
                      className="unfollow-button"
                      value="Unfollow"
                      id="jest-comp-button"
                    >
                      View
                    </button>
                  </a>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
