import React, { Component } from "react";
import "../styles/Markets.css";
import {
  exchangeIndexVis,
  getIndexName,
  getGainers,
  getSectors
} from "../actions/Markets";
import { connect } from "react-redux";
import Table from "./TickerTable";
import Loader from "react-loader-spinner";

export class Markets extends Component {
  state = {
    sectorName: "Industrials"
  };
  componentDidMount() {
    this.props.getIndexName();
    this.props.getGainers("Industrials");
    this.props.getSectors();

    this.props.exchangeIndexVis("DJI");
  }

  setSector = sector => {
    this.setState({ sectorName: sector });
    this.props.getGainers(sector);
  };

  OnButtonClick = () => {
    document.getElementById("myDropdown").classList.toggle("show");

    window.onclick = function(event) {
      if (!event.target.matches(".dropbtn")) {
        var dropdowns = document.getElementsByClassName("dropdown-content");

        var i;
        for (i = 0; i < dropdowns.length; i++) {
          var openDropdown = dropdowns[i];
          if (openDropdown.classList.contains("show")) {
            openDropdown.classList.remove("show");
          }
        }
      }
    };
  };

  render() {
    let gainerData = this.props.gainers
      ? this.props.gainers.gainers
        ? this.props.gainers.gainers.length > 0
          ? this.props.gainers.gainers
          : []
        : []
      : [];
    console.log("gainerdata", gainerData);
    let loserData = this.props.gainers
      ? this.props.gainers.losers
        ? this.props.gainers.losers.length > 0
          ? this.props.gainers.losers
          : []
        : []
      : [];

    return (
      <div>
        {/* {console.log("From Markets ", this.props.indexName)} */}

        <div className="market__container">
          <div className="market__grid">
            {/* <nav className="navbar">nav</nav> */}

            <div className="ticker">
              <h2 style={{ color: "#ff3366" }}>Indexes</h2>
              <div className="ticker-table">
                {this.props.isloading ? (
                  <div
                    style={{
                      marginTop: "10%"
                    }}
                  >
                    <div>
                      <Loader
                        type="Bars"
                        color="#15ddd6"
                        height="100"
                        width="70"
                      />
                      <span
                        style={{
                          marginTop: "40px",
                          color: "#15ddd6"
                        }}
                      >
                        Loading Indexes...
                      </span>
                    </div>
                  </div>
                ) : (
                  <table className="markets-table">
                    <Table
                      tableHeaders={[
                        "Indexes",
                        "Change Percent",
                        "Closing Price"
                      ]}
                      tableData={this.props.indexName}
                    />
                  </table>
                )}
              </div>
            </div>
            <div className="vis">
              <iframe
                frameBorder="0"
                // scrolling="0"
                width="100%"
                height="350"
                src={this.props.indexesVis}
                autosize="true"
                link="false"
                modebar="false"
                logo="false"
              ></iframe>
            </div>
            <div className="sector">
              <div className="dropdown">
                <button
                  onClick={this.OnButtonClick}
                  class="dropbtn"
                  id="dropdown-btn"
                >
                  <i className="droparrow"></i> {this.state.sectorName}
                </button>
                <div
                  id="myDropdown"
                  ref="drop"
                  class="dropdown-content"
                  ref="dropbtn"
                >
                  {this.props.sectors.map(sector => (
                    <a id="anchor" onClick={() => this.setSector(sector)}>
                      {sector}
                    </a>
                  ))}
                </div>
              </div>
            </div>

            <div className="gainer">
              <h2 style={{ color: "#ff3366" }}>Top Gainers</h2>

              {this.props.isloading ? (
                <div
                  style={{
                    marginTop: "10%"
                  }}
                >
                  <div>
                    <Loader
                      type="Bars"
                      color="#15ddd6"
                      height="100"
                      width="70"
                    />
                    <span
                      style={{
                        marginTop: "40px",
                        color: "#15ddd6"
                      }}
                    >
                      Loading Stocks...
                    </span>
                  </div>
                </div>
              ) : (
                <Table
                  tableHeaders={[
                    "TickerName",
                    "Change Percent",
                    "Maket Cap",
                    "Share Price"
                  ]}
                  tableData={gainerData}
                  // isIndex={this.props.gainers.isIndex}
                />
              )}
            </div>

            <div className="loser">
              <h2 style={{ color: "#ff3366" }}>Top Losers</h2>

              {this.props.isloading ? (
                <div
                  className="gainerLoader"
                  style={{
                    marginTop: "10%"
                  }}
                >
                  <div>
                    <Loader
                      type="Bars"
                      color="#15ddd6"
                      height="100"
                      width="70"
                    />
                    <span
                      style={{
                        marginTop: "40px",
                        color: "#15ddd6"
                      }}
                    >
                      Loading Stocks...
                    </span>
                  </div>
                </div>
              ) : (
                <Table
                  tableHeaders={[
                    "TickerName",
                    "Change Percent",
                    "Maket Cap",
                    "Share Price"
                  ]}
                  tableData={loserData}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  indexName: state.MarketsReducer.indexName,
  indexesVis: state.MarketsReducer.indexesVis,
  gainers: state.MarketsReducer.gainers,
  isloading: state.LoadingReducer.isLoading,
  sectors: state.MarketsReducer.sectors
});

export default connect(mapStateToProps, {
  exchangeIndexVis,
  getIndexName,
  getGainers,
  getSectors
})(Markets);
