import React, { Component } from "react";
import "../styles/RegisterUser.css";
import { connect } from "react-redux";
import { sendOtp, verifyOtpForReset, clearReducer } from "../actions/User";
import { IoIosCheckmark } from "react-icons/io";
const emailRegex = require("email-regex");

export class ResetPassword extends Component {
  componentDidMount() {
    this.props.clearReducer();
  }
  state = {
    sendOTPClicked: false,
    email: "",
    password: "",
    conf_password: "",
    otp: "",
    errorMessage: ""
  };
  OnHandleChange = event => {
    const { value, name } = event.target;
    this.setState({
      [name]: value
    });
  };
  // pranav: 5/11/19: onSendOTPClicked function first takes email entered by user from state and calls the api if the state is not empty
  onSendOTPClicked = () => {
    let user = { email: this.state.email };
    if (this.state.email === "") {
      this.setState({ errorMessage: "Email can't be blank" });
    } else {
      this.setState({ sendOTPClicked: true });

      this.props.sendOtp(user);
    }
  };
  // pranav: 5/11/19:onResetClick function first takes the user entered otp and other details from state then calls the api to verity otp
  onResetPClick = () => {
    let otp = { otp: this.state.otp };
    let user = {
      email: this.state.email,
      password: this.state.password,
      conf_password: this.state.conf_password
    };
    this.props.verifyOtpForReset(otp, user, this.props.history);
  };
  render() {
    return (
      <div classNameName="Register-main-wrapper1">
        <div className="login-box">
          <div className="lb-header">
            <p className="Reset-header">Let's Reset Your Pasword</p>
          </div>
          <hr />

          <form className="email-signup" novalidate>
            <div className="u-form-group">
              {emailRegex({ exact: true }).test(this.state.email) ? (
                <IoIosCheckmark className="check-icon" />
              ) : null}
              <input
                autocomplete="off"
                id="reset-email-input"
                name="email"
                minLength="8"
                maxLength="50"
                type="email"
                required
                onChange={this.OnHandleChange}
                value={this.state.email}
              />
              {/* Pranav: 5/11/19: here i am using translate property on label if the input field is not empty*/}
              {this.state.email !== "" ? (
                <label
                  style={{
                    transform: "translateY(-28px)"
                  }}
                  className="Placeholder-Register"
                >
                  Email
                </label>
              ) : (
                <label className="Placeholder-Register">Email</label>
              )}
            </div>
            <div className="u-form-group">
              {this.state.password.length > 7 ? (
                <IoIosCheckmark className="check-icon" />
              ) : null}
              <input
                autocomplete="off"
                id="reset-password-input"
                name="password"
                required
                minLength="8"
                maxLength="20"
                type="password"
                value={this.state.password}
                onChange={this.OnHandleChange}
              />
              {this.state.password !== "" ? (
                <label
                  style={{
                    transform: "translateY(-28px)"
                  }}
                  className="Placeholder-Register9"
                >
                  Password
                </label>
              ) : (
                <label className="Placeholder-Register9">Password</label>
              )}
            </div>
            <div className="u-form-group">
              {this.state.conf_password.length > 7 &&
              this.state.password === this.state.conf_password ? (
                <IoIosCheckmark className="check-icon" />
              ) : null}
              <input
                autocomplete="off"
                id="reset-conf-password-input"
                name="conf_password"
                
                minLength="8"
                maxLength="20"
                type="password"
                required
                onChange={this.OnHandleChange}
                value={this.state.conf_password}
              />{" "}
              {this.state.conf_password !== "" ? (
                <label
                  style={{
                    transform: "translateY(-28px)"
                  }}
                  className="Placeholder-Register10"
                >
                  Conf Password
                </label>
              ) : (
                <label className="Placeholder-Register10">Conf Password</label>
              )}
            </div>
            {/* Pranav: 5/11/19:here iam first checkin state of if the send otp button is clicked or not..if its  clicked
              and below mentioned all states holds some value in that case  below code will be executed */}

            {this.state.sendOTPClicked === true &&
            this.state.email &&
            this.state.password &&
            this.state.conf_password &&
            this.state.password === this.state.conf_password ? (
              <div className="u-form-group">
                <input
                  style={{
                    WebkitAppearance: "none",
                    margin: 0,
                    MozAppearance: "textfield"
                  }}
                  autocomplete="off"
                  id="reset-otp-input"
                  name="otp"
                  type="number"
                  minLength="4"
                  maxLength="4"
                  required
                  onChange={this.OnHandleChange}
                  value={this.state.otp}
                />
                {this.state.otp !== "" ? (
                  <label
                    style={{
                      transform: "translateY(-28px)"
                    }}
                    className="Placeholder-Register11"
                  >
                    OTP
                  </label>
                ) : (
                  <label className="Placeholder-Register11">OTP</label>
                )}

                <button
                  id="reset-button"
                  type="button"
                  className="Register-signup"
                  onClick={this.onResetPClick}
                >
                  RESET
                </button>
              </div>
            ) : (
              <div className="u-form-group">
                {/* Pranav: 5/11/19:here iam first checkin state of if the send otp button is clicked or not..if its not clicked then 
            the sendotpclicked state should hold false value in that case below code will be executed */}

                <button
                  id="reset-send-otp"
                  type="submit"
                  className="Register-sendOTP"
                  onClick={this.onSendOTPClicked}
                >
                  SEND OTP
                </button>
              </div>
            )}
            <div className="show-error"> {this.props.error}</div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  users: state.UserReducer.users,
  error: state.UserReducer.error
});

export default connect(mapStateToProps, {
  sendOtp,
  verifyOtpForReset,
  clearReducer
})(ResetPassword);
