import React, { Component } from "react";
import "../styles/UserProfile.css";
import { connect } from "react-redux";
import "../styles/ReusableCards.css";
import avatar from "../assets/avatar-png-1.png";
import like from "../assets/like.svg";
import upload from "../assets/upload.svg";
import jwt from "jsonwebtoken";
import { Link } from "react-router-dom";
import { FiMenu } from "react-icons/fi";

import "../styles/ReusableSearch.css";

export class UserProfileNavigation extends Component {
  componentDidMount() {
    let token = localStorage.getItem("token");
    if (!token) {
      this.props.history.push("/stocks-Login");
    }
  }
  state = {
    myStocksClick: true,
    recommendationsClick: false,
    uploadClick: false,
    checkClicked: false
  };

  render() {
    return (
      <div>
        <input
          className="check-box-for-hamberger"
          type="checkbox"
          onClick={() => {
            this.setState({ checkClicked: true });
          }}
        />
        {this.state.checkClicked === false ? null : (
          <FiMenu
            className="show-hamberger-button"
            onClick={() => {
              this.setState({ checkClicked: false });
            }}
          />
        )}

        {this.state.checkClicked === false ? (
          <div className="main-user-profile-wrapper">
            <div className="user-profile-navigation-wrp">
              <div className="user-logo-name-container">
                <div className="toggle-for-ham">
                  <span></span>
                  <span></span>
                </div>
                <img src={avatar} alt="avatar" className="img-for-user-prof" />
                <p
                  style={{
                    color: "var(--blue-color)",
                    marginTop: "-5px",
                    fontSize: "30px"
                  }}
                >
                  {/* pranav:24/1119:user can enter their name in any format while registration but while showing it on
                  user profile page im converting first letter to captial */}
                  {jwt
                    .decode(localStorage.getItem("token"))
                    .name.charAt(0)
                    .toUpperCase() +
                    jwt.decode(localStorage.getItem("token")).name.slice(1)}
                </p>
              </div>
              <div className="navigation-option-container">
                <Link
                  to="/stocks-user-profile"
                  style={{ color: "var(--blue-color)" }}
                  onClick={() => {
                    this.setState({
                      myStocksClick: true,
                      recommendationsClick: false,
                      uploadClick: false
                    });
                  }}
                >
                  {this.state.myStocksClick === true ? (
                    <p id="border-point-effect">
                      <img
                        className="profile-nav-options-align"
                        alt="stocks"
                        src={like}
                      />{" "}
                      <label className="navigation-options-names-padding">
                        My Stcoks
                      </label>
                    </p>
                  ) : (
                    <p>
                      <img
                        className="profile-nav-options-align"
                        alt="stocks"
                        src={like}
                      />{" "}
                      <label className="navigation-options-names-padding">
                        My Stcoks
                      </label>
                    </p>
                  )}
                  {console.log(jwt.decode(localStorage.getItem("token")))}
                </Link>
                {/* pranav:25/11/19:checking if the user is admin or normal user here i am taking token and checkng isAdmin field is true or 
                false ,if flase then dont show upload button  */}
                {jwt.decode(localStorage.getItem("token")).isAdmin ===
                false ? null : (
                  <Link
                    to="/upload"
                    style={{ color: "var(--blue-color)" }}
                    onClick={() => {
                      this.setState({
                        myStocksClick: false,
                        recommendationsClick: false,
                        uploadClick: true
                      });
                    }}
                  >
                    {this.state.uploadClick === true ? (
                      <p id="border-point-effect">
                        <img
                          className="profile-nav-options-align"
                          alt="stocks"
                          src={upload}
                        />
                        <label> Upload Data</label>{" "}
                      </p>
                    ) : (
                      <p>
                        <img
                          className="profile-nav-options-align"
                          alt="stocks"
                          src={upload}
                        />
                        <label> Upload Data</label>{" "}
                      </p>
                    )}
                  </Link>
                )}
              </div>
            </div>
          </div>
        ) : null}
      </div>
    );
  }
}
const mapStateToProps = state => ({});
export default connect(mapStateToProps, {})(UserProfileNavigation);
