import React, { Component } from "react";
import "../styles/ToolTipComponent.css";
import { FaQuestion } from "react-icons/fa";

export default class TooltipComponent extends Component {
  render() {
    {
      console.log("from tooltip", this.props.text);
    }
    return (
      <div>
        <div class="tooltip__reusable">
          <FaQuestion
            style={{
              backgroundColor: "var(--pink-color)",
              color: "white",
              borderRadius: "50%",
              width: "20px",
              height: "20px",
              padding: "2px"
            }}
          />
          <p id="tooltiptext" class="tooltiptext__reusable">
            {this.props.text}
          </p>
        </div>
      </div>
    );
  }
}
