import React, { Component } from "react";
import "../styles/searchRecommendation.css";
import Chart from "./Chart";
import logo from "../assets/64px-HP_logo_2012.svg.png";
import { getSectors } from "../actions/Markets";
import { advanceSearch } from "../actions/User";
import { connect } from "react-redux";

export class Screener extends Component {
  constructor(props) {
    super(props);
  }
  state = {
    sectorName: "Industrials",
    recommendationResult: this.props.recommendationResult,
    error: this.props.error
  };
  componentWillReceiveProps(nextProps) {
    if (nextProps.recommendationResult != this.state.recommendationResult) {
      console.log(nextProps);
      this.setState({
        recommendationResult: nextProps.recommendationResult
      });
    } else if (nextProps.error != this.state.error) {
      this.setState({ error: nextProps.error, recommendationResult: [] });
    }
  }
  componentDidMount() {
    this.props.getSectors();
    if (!localStorage.getItem("token")) {
      this.props.history.push("/stocks-Login");
    }
  }
  OnButtonClick = () => {
    document.getElementById("myDropdown").classList.toggle("show");
    window.onclick = function(event) {
      if (!event.target.matches(".dropbtn")) {
        var dropdowns = document.getElementsByClassName("dropdown-content");
        var i;
        for (i = 0; i < dropdowns.length; i++) {
          var openDropdown = dropdowns[i];
          if (openDropdown.classList.contains("show")) {
            openDropdown.classList.remove("show");
          }
        }
      }
    };
  };
  setSector = sector => {
    this.setState({ sectorName: sector });
  };
  render() {
    return (
      <div>
        <div className="companies__result">
          <div className="value__company" id="companyCount">
            {this.state.recommendationResult.length > 0
              ? this.state.recommendationResult.length
              : 0}{" "}
            companies found
          </div>
          <div className="dropdown__advance">
            <div className="dropdown">
              <button
                style={{ marginTop: "10px" }}
                onClick={this.OnButtonClick}
                class="dropbtn"
                id="dropdown-btn"
              >
                <i className="droparrow"></i> {this.state.sectorName}
              </button>
              <div id="myDropdown" class="dropdown-content">
                {this.props.sectors.map((sector, index) => (
                  <a
                    id={"anchor" + index}
                    onClick={() => this.setSector(sector)}
                  >
                    {sector}
                  </a>
                ))}
              </div>
            </div>
          </div>
        </div>
        <div className="scrollable_div">
          <div className="MaxContainer" id="Chart">
            <Chart
              sector={this.state.sectorName}
              advanceSearch={this.props.advanceSearch}
            />
          </div>
          <div class="scrollable__result">
            {this.state.recommendationResult.length > 0 ? (
              <div className="result__card__grid" id="resultCardGrid">
                {this.state.recommendationResult.map((value, index) => (
                  <div className="result__card">
                    <div className="company__name_parent">
                      <div className="company__logo">
                        <img
                          id={"logo" + index}
                          className="logo-image"
                          alt="logo"
                          src={
                            value.logo
                              ? "data:image/jpeg;base64," + value.logo
                              : logo
                          }
                        />
                      </div>
                      <div className="company__name">
                        <p
                          style={{ color: "var(--blue-color)" }}
                          id={"tickerName" + index}
                        >
                          {value.ticker_name}
                        </p>
                        <p
                          style={{ color: "var(--blue-color)" }}
                          id={"industry" + index}
                        >
                          {value.industry}
                        </p>
                      </div>
                    </div>
                    <div className="values">
                      <div className="company__stats">
                        <p
                          id={"sharePrice" + index}
                          style={{
                            color: "var(--blue-color)",
                            marginLeft: "20px",
                            marginTop: "20px",
                            textAlign: "left",
                            fontSize: "20px"
                          }}
                        >
                          {value.shareprice} usd
                        </p>
                        <p
                          id={"date" + index}
                          style={{
                            color: "white",
                            textAlign: "left",
                            marginLeft: "19px"
                          }}
                        >
                          at {value.date}
                        </p>
                      </div>
                      <div className="company__button">
                        <button
                          onClick={() => {
                            this.props.history.push(
                              "/stocks/" + value.ticker_id
                            );
                          }}
                        >
                          View
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="error__message" id="errrorMsg">
                {this.state.error}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  sectors: state.MarketsReducer.sectors,
  recommendationResult: state.UserReducer.recommendationResult,
  error: state.UserReducer.error
});

export default connect(mapStateToProps, {
  getSectors,
  advanceSearch
})(Screener);
