import {
  EXCHANGE_INDEX_VIS,
  INDEX_NAME,
  GAINERS,
  SECTORS
} from "../actions/Types";

const initialState = {
  indexesVis: [],
  indexName: [],
  gainers: [],
  sectors: []
};

export default function(state = initialState, action) {
  switch (action.type) {
    case EXCHANGE_INDEX_VIS:
      return {
        ...state,
        indexesVis: action.payload
      };
    case INDEX_NAME:
      return {
        ...state,
        indexName: action.payload
      };
    case GAINERS:
      return {
        ...state,
        gainers: action.payload
      };
    case SECTORS:
      return {
        ...state,
        sectors: action.payload
      };

    default:
      return state;
  }
}
