import {
  GET_LIST_OF_COMPANIES,
  GET_SECTOR_OR_INDUSTRIES
} from "../actions/Types";

const initialState = {
  companies: [],
  sector_industry: []
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_LIST_OF_COMPANIES:
      return {
        ...state,
        companies: action.payload
      };
    case GET_SECTOR_OR_INDUSTRIES:
      return {
        ...state,
        sector_industry: action.payload
      };

    default:
      return state;
  }
}
