import React, { Component } from "react";
import { Radar } from "react-chartjs-2";
import "chartjs-plugin-dragdata";

export default class Chart extends Component {
  constructor(props) {
    super(props);
  }

  //Local  function to update state as soon as user drags the radar circle on the radar chart -Rohan 25/11/19
  updateState = (index, value) => {
    let newData = this.state.data;
    newData.datasets[0].data[index] = value;
    this.setState({ data: newData });
  };

  findStock = () => {
    let newData = this.state.data.datasets[0].data;
    let term = {
      sectorData: this.props.sector,
      health: newData[0],
      profit: newData[1],
      marketCap: newData[2],
      revenues: newData[3],
      assets: newData[4]
    };
    this.props.advanceSearch(term);
  };
  state = {
    data: {
      type: "radar",
      labels: [
        "Health",
        "Company Profit",
        "Market Capital",
        "Growth",
        "Assets"
      ],
      datasets: [
        {
          // Data points for radar axes
          data: [25, 50, 25, 5, 20],
          backgroundColor: "rgba(255,51,102,0.8)",
          borderColor: "rgba(255,51,102,1)",
          pointBackgroundColor: "rgba(255,51,102,1)",
          borderWidth: 2,
          pointHitRadius: 15,
          pointHoverRadius: 5,
          pointHoverBorderWidth: 3,
          pointRotation: 45,
          pointBorderWidth: 1.2
        }
      ]
    }
  };

  // Dynamically sending data to the chart component -Rohan 25/11/19
  getData = canvas => {
    let data = this.state.data;
    return data;
  };
  render() {
    return (
      <div>
        <Radar
          options={{
            title: {
              display: true,
              text: "Trailing Tweleve Months Data",
              fontColor: "white",
              fontSize: 25
            },
            // disable tooltips -Rohan 25/11/19
            tooltips: {
              enabled: true
            },
            legend: { display: false, labels: { fontColor: "white" } },

            scale: {
              pointLabels: {
                fontColor: "white",
                fontSize: 15
              },
              ticks: {
                display: false,
                max: 100,
                min: 0,
                stepSize: 10
              },
              angleLines: { color: "#c0c7da" },
              gridLines: { color: "#c0c7da" },
              points: { color: "white" }
            },
            type: "radar",
            fill: "true",
            responsive: true,
            dragData: true,
            dragX: false,
            dragDataRound: false,
            roundStrokes: true,
            pointBackgroundColor: "rgba(20, 20, 30)",

            // Call to local function to set state when user drags the radar points -Rohan 25/11/19
            onDrag: (e, datasetIndex, index, value) => {
              this.updateState(index, value);
            },

            // Call to cuntion to get results as soon as user has finished dragging the points -Rohan 25/11/19
            onDragEnd: (e, datasetIndex, index, value) => {
              console.log(datasetIndex, index, value);
              this.findStock();
            }
          }}
          // Supplying data to the radar chart using local function -Rohan 25/11/19
          data={this.getData}
        />
      </div>
    );
  }
}
