import React, { Component } from "react";
import { getReports } from "../actions/StockSpecificAction";
import { connect } from "react-redux";
import "../styles/Reports.css";
import ReportsTable from "./ReportsTable";
import { FaDownload } from "react-icons/fa";

export class ReportsPage extends Component {
  componentWillReceiveProps(nextProps) {
    // console.log(nextProps);
    if (nextProps.tickerName != this.props.tickerName) {
      let year = {
        year: 2019
      };
      this.props.getReports(year, this.props.tickerId);
    }
  }
  componentDidMount() {
    let year = {
      year: 2019
    };
    // Call to the reports function to get latest reports -Rohan 19/11/19
    this.props.getReports(year, this.props.tickerId);
  }
  downloadReportForTicker = () => {
    let reports = {
      report: this.props.reports
    };
    // Using the fetch api to call the api for download option -Rohan 21/11/19
    fetch("http://localhost:5000/apis/StockSpecificPage/download-report", {
      method: "POST",
      // Converting the reports object into json format and sending it with headers -Rohan 21/11/19
      body: JSON.stringify(reports),
      headers: { "Content-Type": "application/json" }
    }).then(response => {
      response.blob().then(blob => {
        // Createing a object url for browser to provide download option for the user -Rohan 21/11/19
        let url = window.URL.createObjectURL(blob);
        let a = document.createElement("a");
        a.href = url;
        a.download =
          this.props.tickerName +
          "(" +
          this.state.reportYear +
          ")" +
          Date.now() +
          ".csv";
        a.click();
      });
    });
  };
  state = {
    reportYear: 2019,
    // Declaring a css class requiredd for manupaliting the ddrop down -Rohan 19/11/19
    dropDownClass: "reports-year-option"
  };
  render() {
    let years = [
      2019,
      2018,
      2017,
      2016,
      2015,
      2014,
      2013,
      2012,
      2011,
      2010,
      2009,
      2008
    ];
    return (
      <div>
        <div className="dropdown__parent">
          <div className="dropdown-container">
            <span
              id="download"
              // On click of button calling the api-Rohan 21/11/19
              onClick={this.downloadReportForTicker}
            >
              <FaDownload
                style={{
                  cursor: "pointer",
                  color: "var(--pink-color)",
                  fontSize: "25px",
                  marginRight: "20px"
                }}
              />
            </span>
            <div
              id="dropdown"
              className="reports-dropdown"
              onClick={() => {
                let year = {
                  year: this.state.reportYear
                };
                // Manupilating the css class on click of the dropdown button -Rohan 21/11/19
                this.setState({ dropDownClass: " " + "showDisplay" });
                this.props.getReports(year, 9);
              }}
            >
              {this.state.reportYear} <i className="droparrow"></i>
            </div>
            <div
              className={this.state.dropDownClass}
              style={{ position: "absolute" }}
            >
              <ul>
                {/* Mapping the years array for the user in the drop down -Rohan 21/11/19 */}
                {years.map((currentYear, index) => (
                  <li
                    id={"dropdownYear" + index}
                    onClick={() => {
                      let year = {
                        year: currentYear
                      };
                      // Callingg tghe reports action with selected year -Rohan 21/11/19
                      this.props.getReports(year, 9);
                      this.setState({
                        reportYear: currentYear,
                        dropDownClass: "noDisplay reports-year-option"
                      });
                    }}
                  >
                    {currentYear}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
        <div className="Reports">
          {/*  Various Call to the reuasable component with headers and values -Rohan 21/11/19 */}
          <h2 id="BalanceSheet">Balance Sheet</h2>
          <ReportsTable
            headers={[
              "date",
              "Current Assets",
              "Intangible Assets",
              "Cash & Cash Equivalents",
              "GoodWill",
              "Total Assets",
              "Accounts Payable",
              "Receivables",
              "Current Liabilities",
              "Total Liabilities",
              "Preferred Equity",
              "Total Noncurrent Liabilities",
              "Equity Before Minorities"
            ]}
            values={this.props.reports}
          />
          <h2 id="PL">Profit and Loss Statement</h2>
          <ReportsTable
            headers={[
              "date",
              "Revenues",
              "EBIT",
              "Net Profit",
              "R&D",
              "Income Taxes"
            ]}
            values={this.props.reports}
          />
          <h2 id="CashFlow">Cash Flow</h2>
          <ReportsTable
            headers={[
              "date",
              "Cash From Operating Activities",
              "Cash From Investing Activities",
              "Cash From Investing Activities",
              "Free Cash Flow",
              "Depreciation & Amortisation",
              "Dividends",
              "Net Change in PP&E & Intangibles",
              "Net Change in Cash",
              "Investing Cash Flow",
              "Financing Cash Flow",
              "Change in Working Capital"
            ]}
            values={this.props.reports}
          />
          <h2 id="Ratios"> Ratios </h2>
          <ReportsTable
            headers={[
              "date",
              "Liablities To Equity Ratio",
              "Debt to Assets Ratio",
              "Current Ratio",
              "EV / EBITDA",
              "EV / Sales",
              "Operating Income / EV"
            ]}
            values={this.props.reports}
          />
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  reports: state.StockReducer.reports
});
// Connecting the call to action -Rohan 16/11/19
export default connect(mapStateToProps, { getReports })(ReportsPage);
