import React, { Component } from "react";
import "../styles/SectorAndMarketMovers.css";
import { getSectorAndIndustries } from "../actions/StocksLandingPage";
import { connect } from "react-redux";
import Table from "./TickerTable";
import Loader from "react-loader-spinner";
import Pagination from "./Pagination";

export class SectorAndIndustry extends Component {
  state = {
    is_sector: true,
    search_text: "",
    currentCompanyPage: 1,
    companiesPerPage: 10
  };

  componentDidMount() {
    this.props.getSectorAndIndustries("sector");
  }

  //   [Yatin] 18/11/2019 CREATED - To handle the input change this method is used
  //   [Yatin] 25/11/2019 MODIFIED - Setting the current page to 1 so search can
  //   done on any page of the pagination.
  OnChange = event => {
    this.setState({
      search_text: event.target.value.substr(0, 30),
      currentCompanyPage: 1
    });
  };

  // [Yatin] 25/11/2019 CREATED - [Pagination] Onclick changing the page number
  handlePageChange = pageNumber => {
    this.setState({ currentCompanyPage: pageNumber });
  };
  render() {
    // [Yatin] 18/11/2019 CREATED - Reducers state result is stored in the filteredCompanies
    // when any text is inserted the data present in the state of reducer is filtered and displayed
    // on the page
    let filteredCompanies;
    if (this.props.sector_industry) {
      filteredCompanies = this.props.sector_industry.filter(company => {
        // [Yatin] 20/11/2019 CREATED - Excluding the null values
        return company.ticker_name === null
          ? null
          : company.ticker_name
              .toLowerCase()
              .indexOf(this.state.search_text.toLowerCase()) !== -1;
      });
    }

    // [Yatin] 25/11/2019 CREATED - [Pagination] Fetching the first and last companies index
    // and slicing to get current companies
    let indexOfLastCompany =
      this.state.currentCompanyPage * this.state.companiesPerPage;
    let indexOfFirstCompany = indexOfLastCompany - this.state.companiesPerPage;
    let currentCompanies =
      filteredCompanies !== null
        ? filteredCompanies.slice(indexOfFirstCompany, indexOfLastCompany)
        : null;
    return (
      <div>
        {/* [Yatin] 16/11/2019 CREATED - When 'Sector&Indtries' button is  clicked(set to false) then the page will 
        load the content of Sector&Indtries having sector and Indtries data/content  */}
        <div className="sector-industry-container">
          <div className="sector-industry-box">
            <div className="sector-industry-options">
              <div
                id="sector-tab"
                //   [Yatin] 16/11/2019 CREATED - In the 'Sector&Industries' page when 'Sector' button is clicked then
                // the is_sector state will be set to true, this is done to show the content of Sector
                //   [Yatin] 20/11/2019 MODIFIED - On click of tab the serach will set to empty
                // this is done to ensure previous tabs serach text does'nt affect the newly clicked tab
                onClick={() => {
                  this.setState({
                    is_sector: true,
                    search_text: ""
                  });
                  this.props.getSectorAndIndustries("sector");
                }}
                //   [Yatin] 16/11/2019 CREATED - In the 'Sector&Indtries' page when 'Sector' button is clicked then
                // css property will change with the text color as blue, this is done to show
                // the active selected tab on the page
                className={
                  this.state.is_sector === true ? "onclick-active-color" : null
                }
              >
                Sector
              </div>
              <div
                id="industry-tab"
                //   [Yatin] 16/11/2019 CREATED - In the 'Sector&Industries' page when 'Industry' button is clicked then
                // the is_sector state will be set to false, this is done to show the content of Industries
                //   [Yatin] 20/11/2019 MODIFIED - On click of tab the serach will set to empty
                // this is done to ensure previous tabs serach text does'nt affect the newly clicked tab
                onClick={() => {
                  this.setState({
                    is_sector: false,
                    search_text: ""
                  });
                  this.props.getSectorAndIndustries("industry");
                }}
                //   [Yatin] 16/11/2019 CREATED - In the 'Sector&Indtries' page when 'Industry' button is clicked then
                // css property will change with the text color as blue, this is done to show
                // the active selected tab on the page
                className={
                  this.state.is_sector === false ? "onclick-active-color" : null
                }
              >
                Industry
              </div>
            </div>
            <div
              id="sector-industry-search"
              className="stocks-main-search-wrapper"
            >
              <div className="stocks-search-container">
                <input
                  id="industry-search-box-input"
                  name="search_text"
                  type="text"
                  value={this.state.search_text}
                  className="stocks-search-input"
                  //[Yatin] 21/11/2019 CREATED - The place holder text will change
                  // when user is on Sector page then "Search Sector" will be in the search placeholder
                  // when user is on Industry page then "Search Industry" will be in the search placeholder
                  placeholder={
                    this.state.is_sector === true
                      ? "Search Sector"
                      : "Search Industry"
                  }
                  onChange={this.OnChange}
                />
                <button
                  id="industry-search-button"
                  className="stocks-search-button"
                />
              </div>
            </div>
          </div>
          {/* [Yatin] 18/11/2019 CREATED - Added Loader when data is loadinga and mapped
          to the reusable tables */}
          {this.props.isloading ? (
            <div
              className="loader-position"
              style={{
                height: 300 + "px",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <div>
                <Loader type="Bars" color="#15ddd6" height="50" width="70" />
                <span
                  style={{
                    marginTop: "40px",
                    color: "#15ddd6"
                  }}
                >
                  {/* [Yatin] 21/11/2019 CREATED - The Loaders text changes based on the page
                    such as loading sector page or Industry page */}
                  {this.state.is_sector === true ? (
                    <>Loading Sector Stocks....</>
                  ) : (
                    <>Loading Industries Stocks.... </>
                  )}
                </span>
              </div>
            </div>
          ) : (
            <>
              {this.state.is_sector === true ? (
                <>
                  {/* [Yatin] 16/11/2019 CREATED - Reusing the Table */}
                  {/*  [Yatin] 20/11/2019 MODIFIED - Checking the length of reducer state if length is 0 then
                 a message will be displayed saying 'No Result Found' */}
                  {filteredCompanies.length > 0 ? (
                    <Table
                      tableHeaders={[
                        "Sector",
                        "Market Cap (M)",
                        "Change",
                        "% Change",
                        "No. of Industries"
                      ]}
                      tableData={currentCompanies}
                      isIndex={false}
                    />
                  ) : (
                    <div className="no-result-found"> No Result Found</div>
                  )}
                </>
              ) : (
                <>
                  {/* [Yatin] 16/11/2019 CREATED - Reusing the Table */}
                  {/*  [Yatin] 20/11/2019 MODIFIED - Checking the length of reducer state if length is 0 then
                 a message will be displayed saying 'No Result Found' */}
                  {filteredCompanies.length > 0 ? (
                    <Table
                      tableHeaders={[
                        "Industry",
                        "Market Cap (M)",
                        "Change",
                        "% Change",
                        "Sector"
                      ]}
                      tableData={currentCompanies}
                      isIndex={false}
                    />
                  ) : (
                    <div className="no-result-found"> No Result Found</div>
                  )}
                </>
              )}
            </>
          )}
          {/* [Yatin] 25/11/2019 CREATED - When data is loading the pagination should not appear by default on the page */}
          {this.props.isloading ? null : (
            // {/* [Yatin] 24/11/2019 CREATED - [Pagination] When length is more than 11 rows then only pagination will apper*/}
            <>
              {filteredCompanies.length >= 11 ? (
                <>
                  <Pagination
                    companiesPerPage={this.state.companiesPerPage}
                    totalCompanies={filteredCompanies.length}
                    handlePageChange={this.handlePageChange}
                    currentCompanyPage={this.state.currentCompanyPage}
                  />
                </>
              ) : null}
            </>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  sector_industry: state.StocksCompanyReducer.sector_industry,
  isloading: state.LoadingReducer.isLoading
});

export default connect(mapStateToProps, {
  getSectorAndIndustries
})(SectorAndIndustry);
