import React, { Component } from "react";
import "../styles/Pagination.css";

export default class Pagination extends Component {
  render() {
    let pageNumbers = [];
    // [Yatin] 25/11/2019 CREATED - Dividing the total pages with the per page
    // example 50/10 = 5 pages, then pushing the count to an empty array declared above
    for (
      let i = 1;
      i <= Math.ceil(this.props.totalCompanies / this.props.companiesPerPage);
      i++
    ) {
      pageNumbers.push(i);
    }
    return (
      <div className="pagination-on-stocks">
        <ul>
          {/* <a
            id="back-arrow"
            // className={
            //   this.props.currentCompanyPage === number
            //     ? "pagination-active-page"
            //     : null
            // }
          >
            &laquo;
          </a> */}
          {/* [Yatin] 25/11/2019 CREATED - OnClick of each page the page number will be updated
          by the handlePage method with the current page number sent as a props*/}
          {pageNumbers.map(number => (
            <a
              onClick={() => {
                this.props.handlePageChange(number);
              }}
              // [Yatin] 25/11/2019 CREATED - Background color css property will be applied
              // when current page is equal to the page number received as a props from handle
              // change method.
              className={
                this.props.currentCompanyPage === number
                  ? "pagination-active-page"
                  : null
              }
            >
              {number}
            </a>
          ))}
          {/* <a
            id="forward-arrow"
            // className={
            //   this.props.currentCompanyPage === number
            //     ? "pagination-active-page"
            //     : null
            // }
          >
            &raquo;
          </a> */}
        </ul>
      </div>
    );
  }
}
