import React, { Component } from "react";

// import OverviewPage from "./OverviewPage";
import ReportPage from "./ReportsPage";
import CompetetorPage from "./CompetetorsPage";
import Dashboard from "./Dashboard";

import "../styles/StockSecondaryNavbar.css";
import default_image from "../assets/ticker-default-image.png";
import StarRatings from "react-star-ratings";
import jwt from "jsonwebtoken";
import {
  getTickerInfo,
  followTicker,
  unFollowTicker,
  getCompetetors,
  getUserData
} from "../actions/StockSpecificAction";
import { connect } from "react-redux";
import Loader from "react-loader-spinner";

export class StockSpecificPage extends Component {
  state = {
    rating: "",
    button_text: "Follow",
    overiew_sec: true,
    reports_sec: false,
    competetors_sec: false,
    showErrorMsg: false
  };

  showToolTip = () => {
    this.setState({ showErrorMsg: true });

    setTimeout(() => {
      this.setState({ showErrorMsg: false });
    }, 2000);
  };

  onFollowButtonClicked = () => {
    let token = localStorage.getItem("token");
    const decodedToken = jwt.decode(token);

    //if the state has button text FOLLOW then it will change the button text to unFollow and call the api to follow Ticker -Ashfi 13/11/19
    if (this.state.button_text === "Follow") {
      this.setState({ button_text: "UnFollow" });
      let usersTicker = {
        usersTicker: this.props.match.params.ticker_id
      };
      token
        ? this.props.followTicker(decodedToken.id, usersTicker)
        : this.showToolTip();
    }
    //if the state has button text UNFOLLOW then it will change the button text to Follow and call the api to unfollow Ticker -Ashfi 13/11/19
    else {
      this.setState({ button_text: "Follow" });
      let usersTicker = {
        usersTicker: this.props.match.params.ticker_id
      };
      token
        ? this.props.unFollowTicker(decodedToken.id, usersTicker)
        : this.showToolTip();
    }
  };

  componentDidMount() {
    this.props.getTickerInfo(this.props.match.params.ticker_id);

    let token = localStorage.getItem("token");
    const decodedToken = jwt.decode(token);
    //if token is present then only it calls the function userData -Ashfi 13/11/19
    token
      ? this.props.getUserData(decodedToken.id)
      : console.log("no token present");
  }
  // This function is used to manipulate the state if the props are updated -Ashfi
  componentWillUpdate(nextProps, nextState) {
    // if the ticker id in new props is not equal to the ticker id from current props then call the api function with new ticker id - Ashfi
    if (nextProps.match.params.ticker_id != this.props.match.params.ticker_id) {
      this.props.getTickerInfo(nextProps.match.params.ticker_id);
      this.props.getCompetetors(nextProps.match.params.ticker_id, {
        sector: nextProps.tickerDetails.sector
      });
    }
  }

  render() {
    console.log("from stocksnavbar", this.props.tickerDetails);
    let tick_id = this.props.match.params.ticker_id;
    let sector = {
      sector: this.props.tickerDetails.sector
    };
    return (
      <div style={{ backgroundColor: "#1f2435" }}>
        <div className="secondarynav-container">
          <nav className="secondary-navbar-main">
            {this.props.isloading ? (
              <div
                style={{
                  height: 500 + "px",
                  display: "flex",

                  justifyContent: "center"
                }}
              >
                <div style={{ marginTop: "60px" }}>
                  <Loader type="Bars" color="#15ddd6" height="70" width="70" />
                  <span
                    style={{
                      marginTop: "40px",
                      color: "#15ddd6"
                    }}
                  >
                    Loading Stocks...
                  </span>
                </div>
              </div>
            ) : (
              <div className="stock-page-secondary-navbar">
                <div className="ticker__img-name-index-stars-wrapper">
                  <div className="ticker-img-name-container">
                    {/* image name,index name,stars,closing price and details here */}
                    <img
                      src={default_image}
                      alt="default image"
                      id="stock-company-img"
                    />
                    <div className="ticker-img-name-container-2">
                      <h2 id="ticker-name">
                        {" "}
                        {this.props.tickerDetails.company_name
                          ? this.props.tickerDetails.company_name
                          : this.props.tickerDetails.ticker_name}
                      </h2>
                      <span
                        id="stock-secnav-closing-price"
                        style={{ wordSpacing: "5px" }}
                      >
                        <b>
                          {this.props.tickerDetails
                            ? this.props.tickerDetails.share_price
                            : "NA"}
                        </b>
                        <span
                          style={{
                            fontSize: "20px",
                            marginLeft: "5px",
                            paddingLeft: "5px"
                          }}
                        >
                          USD
                        </span>
                        {this.props.tickerDetails.change_percent ? (
                          this.props.tickerDetails.change_percent.substring(
                            0,
                            1
                          ) === "+" ? (
                            <span
                              style={{
                                color: "var(--green-color)",
                                paddingLeft: "5px"
                              }}
                            >
                              ({this.props.tickerDetails.change_percent}) %
                            </span>
                          ) : (
                            <span
                              style={{
                                color: "var(--red-color)",
                                paddingLeft: "5px"
                              }}
                            >
                              ({this.props.tickerDetails.change_percent}) %
                            </span>
                          )
                        ) : null}
                      </span>
                      <span id="date">at {this.props.tickerDetails.date}</span>
                    </div>
                    <StarRatings
                      rating={this.props.tickerDetails.star_rating}
                      starRatedColor="yellow"
                      starEmptyColor="white"
                      numberOfStars={5}
                      name="rating"
                      starSpacing="1px"
                      starDimension="17px"
                    />
                  </div>
                </div>
                <div className="stock-page-nav-links-div">
                  <ul className="stock-nav-tabs">
                    <li
                      onClick={() => {
                        this.setState({
                          overiew_sec: true,
                          reports_sec: false,
                          competetors_sec: false
                        });
                      }}
                      id="jest-overview_sec"
                    >
                      <a
                        href="#overview"
                        className={
                          this.state.overiew_sec ? "active-links" : "not-active"
                        }
                      >
                        Dashboard
                      </a>
                    </li>
                    <li
                      onClick={() => {
                        this.setState({
                          overiew_sec: false,
                          reports_sec: true,
                          competetors_sec: false
                        });
                      }}
                      id="jest-reports_sec"
                    >
                      <a
                        href="#reports"
                        className={
                          this.state.reports_sec ? "active-links" : "not-active"
                        }
                      >
                        Reports
                      </a>
                    </li>
                    <li
                      onClick={() => {
                        this.setState({
                          overiew_sec: false,
                          reports_sec: false,
                          competetors_sec: true
                        });
                        this.props.getCompetetors(tick_id, sector);
                      }}
                      id="jest-competetors_sec"
                    >
                      <a
                        href="#competetors"
                        className={
                          this.state.competetors_sec
                            ? "active-links"
                            : "not-active"
                        }
                      >
                        Competitors
                      </a>
                    </li>
                  </ul>
                </div>
                <div className="stock-ticker-info">
                  <div className="stock-company-info-container">
                    <div className="single-info-div">
                      <span>
                        Sector
                        <br />
                        <span style={{ marginTop: 60 }}>
                          {this.props.tickerDetails.sector}
                        </span>
                      </span>
                    </div>
                    <div className="single-info-div">
                      <span>
                        Industry
                        <br /> {this.props.tickerDetails.industry}
                      </span>
                    </div>
                    <div className="single-info-div">
                      <span>
                        Market Cap
                        <br />
                        <span>&#36;</span>
                        {this.props.tickerDetails.market_capitalisation
                          ? this.props.tickerDetails.market_capitalisation.toFixed(
                              2
                            )
                          : "NA"}{" "}
                        M
                      </span>
                    </div>
                    <div className="single-info-div">
                      <span>
                        EPS
                        <br />{" "}
                        {this.props.tickerDetails.EpsValue
                          ? this.props.tickerDetails.EpsValue.toFixed(6)
                          : "NA"}
                      </span>
                    </div>
                    <div className="single-info-div">
                      <span>
                        PE
                        <br />
                        {this.props.tickerDetails.PEValue
                          ? this.props.tickerDetails.PEValue.toFixed(2)
                          : "NA"}
                      </span>
                    </div>
                  </div>
                  {/* */}
                  {localStorage.getItem("token") ? (
                    <div className="follow-button-div ">
                      <button
                        id={
                          this.props.userFollow
                            ? this.props.userFollow.usersTicker
                              ? this.props.userFollow.usersTicker.includes(
                                  this.props.match.params.ticker_id
                                )
                                ? "unFollow-btn"
                                : "follow-btn"
                              : "follow-btn"
                            : "unFollow-btn"
                        }
                        onClick={this.onFollowButtonClicked}
                      >
                        {this.props.userFollow
                          ? this.props.userFollow.usersTicker
                            ? this.props.userFollow.usersTicker.includes(
                                this.props.match.params.ticker_id
                              )
                              ? "UnFollow"
                              : "Follow"
                            : "Follow"
                          : "UnFollow"}{" "}
                        {this.props.tickerDetails.ticker_name}
                      </button>
                    </div>
                  ) : (
                    <div className="follow-button-div ">
                      <button
                        id={"follow-btn"}
                        onClick={this.onFollowButtonClicked}
                      >
                        {this.props.userFollow
                          ? this.props.userFollow.usersTicker
                            ? this.props.userFollow.usersTicker.includes(
                                this.props.match.params.ticker_id
                              )
                              ? "UnFollow"
                              : "Follow "
                            : "Follow "
                          : ""}{" "}
                        {this.props.tickerDetails.ticker_name}
                      </button>

                      {this.state.showErrorMsg ? (
                        <div id="tooltip" class="left">
                          <i class="up"></i>
                          <div class="tooltip-label">
                            Please login to use this feature
                          </div>
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  )}
                </div>
              </div>
            )}
          </nav>
        </div>

        {this.state.overiew_sec ? (
          <div>
            {this.props.isloading ? (
              <div
                style={{
                  height: 500 + "px",
                  display: "flex",

                  justifyContent: "center"
                }}
              >
                <div style={{ marginTop: "60px" }}>
                  <Loader type="Bars" color="#15ddd6" height="70" width="70" />
                  <span
                    style={{
                      marginTop: "40px",
                      color: "#15ddd6"
                    }}
                  >
                    Loading Stocks...
                  </span>
                </div>
              </div>
            ) : (
              <Dashboard
                tickerId={this.props.match.params}
                ticker_name={this.props.tickerDetails.ticker_name}
                ev_EBITDA={this.props.tickerDetails.ev_EBITDA}
                ev_Sales={this.props.tickerDetails.ev_Sales}
                operating_Income_EV={
                  this.props.tickerDetails.operating_Income_EV
                }
                current_ratio={this.props.tickerDetails.current_ratio}
              />
            )}
          </div>
        ) : this.state.reports_sec ? (
          <div>
            {" "}
            <ReportPage
              tickerId={this.props.match.params.ticker_id}
              tickerName={this.props.tickerDetails.ticker_name}
            />{" "}
          </div>
        ) : this.props.competetors.TopCompetetors ? (
          <div>
            {" "}
            <CompetetorPage
              tickerId={this.props.match.params.ticker_id}
              ticker_name={this.props.tickerDetails.ticker_name}
              ticker_sector={this.props.tickerDetails.sector}
              ticker_competetor={this.props.competetors}
              history={this.props.history}
            />
          </div>
        ) : (
          <div
            style={{
              height: 500 + "px",
              display: "flex",
              marginTop: "50px",
              justifyContent: "center"
            }}
          >
            <div>
              <Loader type="Bars" color="#15ddd6" height="70" width="70" />
              <span
                style={{
                  marginTop: "40px",
                  color: "#15ddd6"
                }}
              >
                Loading Competetors...
              </span>
            </div>
          </div>
        )}
      </div>
    );
  }
}

// mapStateToProps is a function with state parameter which connects to the reducer
const mapStateToProps = state => ({
  tickerDetails: state.StockReducer.tickerDetails,
  isloading: state.LoadingReducer.isLoading,
  userFollow: state.StockReducer.userFollow,
  competetors: state.StockReducer.competetors
});
// connect is a function where is used to connect to the reducer and pass the current class
export default connect(mapStateToProps, {
  getTickerInfo,
  followTicker,
  unFollowTicker,
  getUserData,
  getCompetetors
})(StockSpecificPage);
