import { combineReducers } from "redux";
import indexesVis from "./MarketsReducer";
import users from "./UserReducer";
import stocks from "./StockSpecificReducer";
import indexName from "./MarketsReducer";
import search from "./searchReducer";
import LoadingReducer from "./LoadingReducer";
import companies from "./StocksLandingReducer";

export default combineReducers({
  indexReducer: indexesVis,
  UserReducer: users,
  MarketsReducer: indexName,
  StockReducer: stocks,
  searchReducer: search,
  LoadingReducer,
  StocksCompanyReducer: companies
});
