import { SEARCH_BAR, ERROR } from "./Types";
import axios from "axios";

const url =
  "https://stockit-server.herokuapp.com/apis/searchTicker/search-for-ticker";

// Action for navbar Search ppassing search_term object to the functiona and dispatching it -Rohan 1/11/19
export const navbarSearch = search_term => dispatch => {
  return axios
    .post(url, search_term)
    .then(res => {
      // If promise is returned call the type SEARCH BAR and return payload to the client -Rohan 12/11/19
      dispatch({
        type: SEARCH_BAR,
        payload: res.data.data
      });
    })
    .catch(err => {
      dispatch({
        // If there is an exception then call error type and sedn errror message in the payload -Rohan 12/11/19
        type: ERROR,
        payload: err.response.data.message
      });
    });
};
