import React, { Component } from "react";
import "../styles/dashboard.css";
import {
  getClosingGraph,
  piechart,
  getGauge,
  monteCarlo,
  marketGraph
  //   getClosingGraph
} from "../actions/StockSpecificAction";
import { connect } from "react-redux";
import Tooltip from "./TooltipComponent";

export class Dashboard extends Component {
  componentDidMount() {
    this.props.monteCarlo(this.props.tickerId.ticker_id);
    this.props.piechart(this.props.tickerId.ticker_id);
    this.props.getGauge(this.props.tickerId.ticker_id);
    this.props.marketGraph(this.props.tickerId.ticker_id);
    this.props.getClosingGraph(this.props.tickerId.ticker_id);
  }
  render() {
    return (
      <div style={{ backgroundColor: "#1f2435" }}>
        <div className=" dashboard__container">
          <div className="dash">
            <div className="ratios">
              <div className="ratio1">
                <p>EV/EBIDA</p>
                <p>{this.props.ev_EBITDA ? this.props.ev_EBITDA : "NA"}</p>
              </div>
              <div className="ratio2">
                <p>EV/Sales</p>
                <p>{this.props.ev_Sales ? this.props.ev_Sales : "NA"}</p>
              </div>
              <div className="ratio3">
                <p className="incomePara">Income</p>
                <p>
                  {this.props.operating_Income_EV
                    ? this.props.operating_Income_EV
                    : "NA"}
                </p>
              </div>
              <div className="ratio4">
                <p>Current Ratio</p>
                <p>
                  {this.props.current_ratio ? this.props.current_ratio : "NA"}
                </p>
              </div>
            </div>

            <div className="piechart">
              <div style={{ textAlign: "right" }}>
                <Tooltip text="Piechart shows Assets and Liabilities and their breakdowns"></Tooltip>
              </div>

              <iframe
                frameBorder="0"
                // scrolling="0"
                width="100%"
                height="400"
                src={this.props.piecharts}
                // autosize="true"
                // link="false"
                // modebar="false"
                // logo="false"
              ></iframe>
            </div>
            <div className="closing">
              <div style={{ textAlign: "right" }}>
                <Tooltip text="Annual growth of share price of a particular ticker"></Tooltip>
              </div>
              <iframe
                frameBorder="0"
                // scrolling="0"
                width="100%"
                height="390"
                src={this.props.closingGraph}
                autosize="true"
                link="false"
                modebar="false"
                logo="false"
              ></iframe>
            </div>
            <div className="gauge">
              <div style={{ textAlign: "right" }}>
                <Tooltip text="Prediction of a stock based on indicator names"></Tooltip>
              </div>{" "}
              <h2 style={{ color: "#ff3366" }}>We Suggest</h2>
              <img
                id="gaugeImage"
                style={{ width: "70%" }}
                src={
                  this.props.gauge
                    ? "data:image/jpeg;base64," + this.props.gauge
                    : null
                }
              ></img>
            </div>
            <div className="market-cap">
              <div style={{ textAlign: "right" }}>
                <Tooltip text="Daily Market Cap Change"></Tooltip>
              </div>
              <iframe
                frameBorder="0"
                // scrolling="0"
                width="100%"
                height="300"
                style={
                  {
                    //   backgroundColor: "#1c2030",
                    // boxShadow: "0 4px 6px 0",
                    // borderRadius: "8px"
                  }
                }
                src={this.props.marketcap}
                // autosize="true"
                // link="false"
                // modebar="false"
                // logo="false"
              ></iframe>
            </div>
            <div className="monte-carlo" style={{ boxShadow: "0 4px 6px 0" }}>
              <div style={{ textAlign: "right" }}>
                <Tooltip text="Forecast of range of share prices based on 10 simulations"></Tooltip>
              </div>
              <iframe
                frameBorder="0"
                // scrolling="0"
                width="100%"
                height="300"
                style={
                  {
                    //   backgroundColor: "#1c2030",
                    // boxShadow: "0 4px 6px 0",
                    // borderRadius: "8px"
                    // backgroundColor: "white"
                  }
                }
                src={this.props.montecarlo}
              ></iframe>
              <div></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStatetoProps = state => ({
  // closingGraph: state.StockReducer.closingGraph,
  closingGraph: state.StockReducer.closingGraph,
  piecharts: state.StockReducer.piecharts,
  gauge: state.StockReducer.gauge,
  montecarlo: state.StockReducer.montecarlo,
  marketcap: state.StockReducer.marketcap
});

export default connect(mapStatetoProps, {
  getClosingGraph,
  piechart,
  getGauge,
  monteCarlo,
  marketGraph
})(Dashboard);
