import React, { Component } from "react";
import "../styles/UserProfile.css";
import { connect } from "react-redux";
import "../styles/ReusableCards.css";
import logo from "../assets/64px-HP_logo_2012.svg.png";
import jwt from "jsonwebtoken";
import { followedTickerDetails, followedTickerSearch } from "../actions/User";
import { unFollowTicker } from "../actions/StockSpecificAction";
import "../styles/ReusableSearch.css";
import Loader from "react-loader-spinner";
import UserNavigation from "./UserProfileNavigation";

export class UserProfile extends Component {
  state = {
    searchTerm: "",
    searchClicked: 1,
    unfollowInSearch: 1
  };

  OnChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  componentDidMount() {
    let token = localStorage.getItem("token");
    if (!token) {
      this.props.history.push("/stocks-Login");
    }
    const decodedToken = jwt.decode(token);
    this.props.followedTickerDetails(decodedToken.id);
  }
  render() {
    console.log(this.props);
    let filteredTickers;
    if (this.props.TickerDetails1) {
      filteredTickers = this.props.TickerDetails1.filter(ticker => {
        return ticker.Comapany.ticker_name !== null
          ? ticker.Comapany.ticker_name
              .toLowerCase()
              .indexOf(this.state.searchTerm.toLowerCase()) !== -1
          : null;
      });
    }

    return (
      <div>
        <div className="head-search-bar">
          <div className="main-search-wrapper">
            <div className="search-container">
              <input
                name="searchTerm"
                type="text"
                className="search-input"
                placeholder="Search Following Tickers"
                onChange={this.OnChange}
                value={this.state.searchTerm}
              />
              <button
                id="search-for-followed-ticker"
                className="search-button"
              />
            </div>
          </div>
        </div>
        <div className="main-user-profile-wrapper">
          <UserNavigation />

          <div>
            {/* pranav:20/11/19 : on this i am checking if the tickerdetails reducer is filled or not means if user is already following some tickers or not ,,id not then show some tip  */}
            {this.props.TickerDetails1.length === 0 && !this.props.isloading ? (
              <div classNane="empty-text-div">
                <p className="empty-text-ex">Follow Some Tickers First</p>
              </div>
            ) : null}
          </div>

          {this.props.isloading ? (
            <div className="loader-for-user-prof">
              <div>
                <Loader type="Bars" color="#15ddd6" height="70" width="70" />
                <span
                  style={{
                    marginTop: "40px",
                    color: "#15ddd6"
                  }}
                >
                  Loading followed Ticker...
                </span>
              </div>
            </div>
          ) : (
            <div className="mid-company-cards">
              <div className="grid-container-for-reusable-card">
                {filteredTickers.length > 0 ? (
                  <>
                    {filteredTickers.map(data => (
                      <div className="main-card-container">
                        <div className="flex-company-name">
                          <div className="company-logo">
                            <img
                              className="logo-image"
                              alt="logo"
                              // pranav 2./11/19: here i am using ternary operation to show a default image if theres no image in database
                              src={
                                data.Comapany.ticker_logo == null
                                  ? logo
                                  : "data:image/jpeg;base64," +
                                    data.Comapany.ticker_logo
                              }
                            />{" "}
                          </div>
                          <div className="company-name">
                            <p className="co-name">
                              {data.Comapany.ticker_name}
                            </p>

                            <p className="co-tickers">
                              ({data.Comapany.ticker_name})
                            </p>
                          </div>
                        </div>

                        <div className="flex-company-values">
                          <div className="company-stats">
                            <p className="stock-price">
                              {data.last_Share}{" "}
                              <span style={{ fontSize: "12px" }}>USD</span>
                              {/* pranav 20/11/14 here im using substring function to check first substring of incomming data string 
                          if its in negative then im changing colour of that string to red,if positive then green */}
                              {data.Share.substring(0, 1) === "-" ? (
                                <span
                                  style={{
                                    color: "var(--red-color)"
                                  }}
                                >
                                  ({data.Share}) %
                                </span>
                              ) : (
                                <span
                                  style={{
                                    color: "var(--green-color)"
                                  }}
                                >
                                  ({data.Share}) %
                                </span>
                              )}
                            </p>

                            <p className="price-on-date">
                              at <span> {data.date}</span>{" "}
                            </p>
                          </div>
                          <div className="company-unfollow">
                            <button
                              className="unfollow-button"
                              value="Unfollow"
                              onClick={() => {
                                let token = localStorage.getItem("token");
                                const decodedToken = jwt.decode(token);

                                let ticker_id = data.Comapany.ticker_id.toString();

                                let usersTicker = {
                                  usersTicker: ticker_id
                                };

                                this.props.unFollowTicker(
                                  decodedToken.id,
                                  usersTicker
                                );
                                this.props.followedTickerDetails(
                                  decodedToken.id
                                );
                              }}
                            >
                              Unfollow
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </>
                ) : (
                  <div classNane="empty-text-div">
                    <p className="empty-text-ex">No Result Found</p>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  search_result1: state.UserReducer.search_result1,
  TickerDetails1: state.UserReducer.TickerDetails1,
  isloading: state.LoadingReducer.isLoading
});
export default connect(mapStateToProps, {
  followedTickerDetails,
  unFollowTicker,
  followedTickerSearch
})(UserProfile);
