import {
  GET_TICKER_DETAILS,
  GET_STOCK_CLOSING_GRAPH,
  FOLLOW_TICKER,
  UNFOLLOW_TICKER,
  USER_FOLLOW_DATA,
  ERROR,
  GET_REPORT,
  GET_COMPETETOR,
  PIECHART,
  GAUGE,
  MONTECARLO,
  DOWNLOAD_REPORT,
  MARKET_CAP
} from "../actions/Types";

const initialState = {
  tickerDetails: [[{ sector: "", ticker_name: "" }, {}, {}]],
  closingGraph: [],
  userFollow: [],
  error: "",
  competetors: [],
  reports: [],
  piecharts: [],
  gauge: [],
  montecarlo: [],
  marketcap: []
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_TICKER_DETAILS:
      return {
        ...state,
        tickerDetails: action.payload
      };
    // Case to get graph of a  closing graph for a particular ticker -Ashfi 18/11/19
    case GET_STOCK_CLOSING_GRAPH:
      return {
        ...state,
        closingGraph: action.payload
      };
    // Case to follow  a particular ticker -Ashfi 14/11/19
    case FOLLOW_TICKER:
      return { ...state, userFollow: action.payload };
    case UNFOLLOW_TICKER:
      return state;
    // Case to unfollow  a particular ticker -Ashfi 14/11/19
    case USER_FOLLOW_DATA:
      return {
        ...state,
        userFollow: action.payload
      };
    //
    case GET_COMPETETOR:
      return { ...state, competetors: action.payload };
    // Case to get reports for a particular ticker -Rohan 16/11/19
    case GET_REPORT:
      return { ...state, reports: action.payload };
    case ERROR:
      return {
        ...state,
        error: action.payload
      };
    case PIECHART:
      return {
        ...state,
        piecharts: action.payload
      };
    case GAUGE:
      return {
        ...state,
        gauge: action.payload
      };
    case MONTECARLO:
      return {
        ...state,
        montecarlo: action.payload
      };
    case MARKET_CAP:
      return {
        ...state,
        marketcap: action.payload
      };
    case DOWNLOAD_REPORT:
      return { ...state };
    default:
      return { ...state };
  }
}
