// Rohan gets report for a paricular ticker
export const getReports = (year, ticker_id) => dispatch => {
  return axios
    .post(url + "get-reports/" + ticker_id, year)
    .then(res => {
      dispatch({
        type: GET_REPORT,
        payload: res.data.data
      });
    })
    .catch(err => {
      dispatch({ type: ERROR, payload: err });
    });
};

