import React, { Component } from "react";
import "../styles/ReportsTable.css";

export default class ReportsTable extends Component {
  render() {
    return (
      <div className="reports__table__container">
        <table className="reports__table">
          {/* Mapping the headerrs array -Rohan 19/11/19 */}
          {this.props.headers.map((indicatorName, index) => (
            <tr>
              {/* If header is a date then print Indicator name else print the header -Rohan 19/11/19*/}
              {indicatorName === "date" ? (
                <td className="reportsTable__header" id={"notadate" + index}>
                  Indicator Names
                </td>
              ) : (
                <td
                  className="reports__table__data"
                  id={"indicatorName" + index}
                >
                  {indicatorName}
                </td>
              )}
              <>
                {/* Mapping values which is array of objects -Rohan 19/11/19 */}
                {this.props.values.map((value, index) => (
                  <>
                    {indicatorName === "date" ? (
                      <td className="reportsTable__header" id={"date" + index}>
                        {" "}
                        {/* Converting the date value into normal local string format -Rohan 19/11/19 */}
                        {new Date(value[indicatorName]).toLocaleDateString(
                          "en-IN",
                          {
                            month: "short",
                            year: "2-digit"
                          }
                        )}
                      </td>
                    ) : // If these are the header values then make the text bold -Rohan 19/11/19
                    indicatorName === "Total Assets" ||
                      indicatorName === "Total Liabilities" ||
                      indicatorName === "Equity Before Minorities" ? (
                      <td
                        id={"boldData" + index}
                        style={{ fontWeight: "bold" }}
                        className="reports__table__data"
                      >
                        {/* If the values are absent or null then provide the component with '-' character -Rohan 19/11/19 */}
                        {value[indicatorName] ? value[indicatorName] : "-"}
                      </td>
                    ) : (
                      // If the values are absent or null then provide the component with '-' character -Rohan 19/11/19 *

                      <td className="reports__table__data" id={"data" + index}>
                        {value[indicatorName] ? value[indicatorName] : "-"}
                      </td>
                    )}
                  </>
                ))}
              </>
            </tr>
          ))}
        </table>
      </div>
    );
  }
}
// Rohan -19/11/19
// This component accepts two parameters, headers and values
// headers must be an array of strings
// headers=["column1","column2","column2"]
// Headers must be the columns you want in your table
// values must be array of objects where the headers that your are passing are your properties
// values=[{column1:123,column2:3mcolumn3:123},{column1:123,column2:3mcolumn3:123}]
// The headers and values must be passed in the specified format to get the result
