import React from "react";
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import StocksSpecificPage from "../src/components/StockSpecificPage";
import Navbar from "../src/components/Navbar";
import UserProfile from "../src/components/UserProfile";
import Markets from "../src/components/Markets";
import register from "../src/components/RegisterUser";
import resetPassword from "../src/components/ResetPassword";
import LoginUser from "../src/components/LoginUser";
import StocksLandingNavBar from "../src/components/StocksLandingNavBar";
import AdminUpload from "./components/AdminUpload";
import UserNav from "./components/UserProfileNavigation";

import Dashboard from "./components/Dashboard";
import Screener from "./components/Screener";

function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Route
          exact
          path="/stocks/:ticker_id"
          component={StocksSpecificPage}
        ></Route>
        <Route exact path="/" component={Markets}></Route>
        <Route path="/UserNav" component={UserNav}></Route>
        <Route exact path="/stocks-register" component={register}></Route>
        <Route exact path="/upload" component={AdminUpload}></Route>
        <Route exact path="/stocks-reset" component={resetPassword}></Route>
        <Route exact path="/stocks-Login" component={LoginUser}></Route>
        <Route exact path="/dashboard" component={Dashboard}></Route>
        {/* <Route exact path="/chart" component={Chart}></Route> */}
        <Route exact path="/screener" component={Screener}></Route>
        <Route
          exact
          path="/home/stocks"
          component={StocksLandingNavBar}
        ></Route>

        <Route
          exact
          path="/stocks-user-profile"
          component={UserProfile}
        ></Route>
      </Router>
    </div>
  );
}

export default App;
