import React, { Component } from "react";
import "../styles/RegisterUser.css";
import { connect } from "react-redux";
import { createUsers, sendOtp, verifyOtp, clearReducer } from "../actions/User";
import { IoIosCheckmark } from "react-icons/io";
const emailRegex = require("email-regex");

export class RegisterUser extends Component {
  componentDidMount() {
    this.props.clearReducer();
  }
  state = {
    sendOTPClicked: false,
    email: "",
    name: "",
    password: "",
    conf_password: "",
    otp: "",
    isAdmin: false
  };
  OnHandleChange = event => {
    const { value, name } = event.target;
    this.setState({
      [name]: value
    });
  };

  onSendOTPClicked = () => {
    this.setState({ sendOTPClicked: true });
    let user = { email: this.state.email };
    this.props.sendOtp(user);
  };
  onSignUpClick = () => {
    let otp = { otp: this.state.otp };
    let user = {
      name: String(this.state.name),
      email: String(this.state.email),
      password: String(this.state.password),
      isAdmin: Boolean(this.state.isAdmin)
    };

    if (this.state.password === this.state.conf_password) {
      this.props.verifyOtp(otp, user, this.props.history);
    }
    this.setState({
      loginClicked: false
    });
  };

  onLoginClick = () => {
    this.props.history.push("/stocks-Login");
  };
  render() {
    return (
      <div classNameName="Register-main-wrapper">
        <div className="login-box">
          <div>
            <div className="lb-header">
              <button id="login-box-link" onClick={this.onLoginClick}>
                LOGIN
              </button>
              <button id="signup-box-link1">SIGN UP</button>
            </div>
            <hr />

            <form className="email-signup" novalidate>
              <div className="u-form-group">
                {this.state.name.length > 3 ? (
                  <IoIosCheckmark className="check-icon" />
                ) : null}
                <input
                  autocomplete="off"
                  id="name-input"
                  name="name"
                  required
                  type="text"
                  onChange={this.OnHandleChange}
                  value={this.state.name}
                  minLength="3"
                  maxLength="20"
                />
                {this.state.name !== "" ? (
                  <label
                    style={{
                      transform: "translateY(-28px)"
                    }}
                    className="Placeholder-Register3"
                  >
                    Name
                  </label>
                ) : (
                  <label className="Placeholder-Register3">Name</label>
                )}
              </div>

              <div className="u-form-group">
                {emailRegex({ exact: true }).test(this.state.email) ? (
                  <IoIosCheckmark className="check-icon" />
                ) : null}
                <input
                  autocomplete="off"
                  id="email-input"
                  name="email"
                  required
                  minLength="8"
                  maxLength="50"
                  type="email"
                  onChange={this.OnHandleChange}
                  value={this.state.email}
                />
                {this.state.email !== "" ? (
                  <label
                    style={{
                      transform: "translateY(-28px)"
                    }}
                    className="Placeholder-Register4"
                  >
                    Email
                  </label>
                ) : (
                  <label className="Placeholder-Register4">Email</label>
                )}
              </div>
              <div className="u-form-group">
                {this.state.password.length > 7 ? (
                  <IoIosCheckmark className="check-icon" />
                ) : null}
                <input
                  autocomplete="off"
                  id="password-input"
                  name="password"
                  type="password"
                  minLength="5"
                  maxLength="20"
                  required
                  value={this.state.password}
                  onChange={this.OnHandleChange}
                />
                {this.state.password !== "" ? (
                  <label
                    style={{
                      transform: "translateY(-28px)"
                    }}
                    className="Placeholder-Register5"
                  >
                    Password
                  </label>
                ) : (
                  <label className="Placeholder-Register5">Password</label>
                )}
              </div>
              <div className="u-form-group">
                {this.state.conf_password.length > 7 &&
                this.state.password === this.state.conf_password ? (
                  <IoIosCheckmark className="check-icon" />
                ) : null}
                <input
                  autocomplete="off"
                  id="conf-password-input"
                  required
                  minLength="8"
                  maxLength="20"
                  name="conf_password"
                  type="password"
                  onChange={this.OnHandleChange}
                  value={this.state.conf_password}
                />
                {this.state.conf_password !== "" ? (
                  <label
                    style={{
                      transform: "translateY(-28px)"
                    }}
                    className="Placeholder-Register6"
                  >
                    Confirm Password
                  </label>
                ) : (
                  <label className="Placeholder-Register6">
                    Confirm Password
                  </label>
                )}
              </div>
              {this.state.sendOTPClicked === true &&
              this.state.email &&
              this.state.password &&
              this.state.conf_password &&
              this.state.name &&
              this.state.password === this.state.conf_password ? (
                <div className="u-form-group">
                  <input
                    style={{ WebkitAppearance: "none", margin: 0 }}
                    autocomplete="off"
                    id="otp-input"
                    required
                    name="otp"
                    minLength="4"
                    maxLength="4"
                    type="number"
                    onChange={this.OnHandleChange}
                    value={this.state.otp}
                  />
                  {this.state.otp !== "" ? (
                    <label
                      style={{
                        transform: "translateY(-28px)"
                      }}
                      className="Placeholder-Register7"
                    >
                      OTP
                    </label>
                  ) : (
                    <label className="Placeholder-Register7">OTP</label>
                  )}

                  <button
                    id="signup-register"
                    type="button"
                    className="Register-signup"
                    onClick={this.onSignUpClick}
                  >
                    SIGN UP
                  </button>
                </div>
              ) : (
                <div className="u-form-group">
                  <button
                    id="send-otp-button"
                    type="button"
                    className="Register-sendOTP"
                    onClick={this.onSendOTPClicked}
                    value={this.state.otp}
                  >
                    SEND OTP
                  </button>
                </div>
              )}
            </form>
          </div>

          <div id="server-error-2" className="show-error2">
            {" "}
            {this.props.error}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  users: state.UserReducer.users,
  error: state.UserReducer.error
});

export default connect(mapStateToProps, {
  createUsers,
  sendOtp,
  verifyOtp,
  clearReducer
})(RegisterUser);
