import React, { Component } from "react";
import "../styles/SectorAndMarketMovers.css";
import { getCompanies } from "../actions/StocksLandingPage";
import { connect } from "react-redux";
import Table from "./TickerTable";
import Loader from "react-loader-spinner";
import Pagination from "./Pagination";

export class MarketMovers extends Component {
  state = {
    large_cap: true,
    most_volatile: false,
    search_text: "",
    currentCompanyPage: 1,
    companiesPerPage: 10
  };

  componentDidMount() {
    this.props.getCompanies("largecap");
  }

  //   [Yatin] 18/11/2019 CREATED - To handle the input change this method is used
  //   [Yatin] 25/11/2019 MODIFIED - Setting the current page to 1 so search can be
  //   done on any page of the pagination.
  OnChange = event => {
    this.setState({
      search_text: event.target.value.substr(0, 15),
      currentCompanyPage: 1
    });
  };

  // [Yatin] 24/11/2019 CREATED - [Pagination] Onclick changing the page number
  handlePageChange = pageNumber => {
    this.setState({ currentCompanyPage: pageNumber });
  };
  render() {
    // [Yatin] 18/11/2019 CREATED - Reducers state result is stored in the filteredCompanies
    // when any text is inserted the data present in the state of reducer is filtered and displayed
    // on the page
    let filteredCompanies;
    if (this.props.companies) {
      filteredCompanies = this.props.companies.filter(company => {
        // [Yatin] 20/11/2019 CREATED - Excluding the null values
        return company.ticker_name === null
          ? null
          : company.ticker_name
              .toLowerCase()
              .indexOf(this.state.search_text.toLowerCase()) !== -1;
      });
    }
    // [Yatin] 24/11/2019 CREATED - [Pagination] Fetching the first and last companies index
    // and slicing to get current companies
    let indexOfLastCompany =
      this.state.currentCompanyPage * this.state.companiesPerPage;
    let indexOfFirstCompany = indexOfLastCompany - this.state.companiesPerPage;
    let currentCompanies =
      filteredCompanies !== null
        ? filteredCompanies.slice(indexOfFirstCompany, indexOfLastCompany)
        : null;

    return (
      <div>
        <div className="market-movers-container">
          <div className="market-movers-box">
            <div className="market-movers-options">
              <div
                id="market-large-cap"
                //   [Yatin] 14/11/2019 CREATED - In the 'Market Mover' page when 'Large Cap' button is clicked then
                // the large_cap state will be set to true, this is done to show the content of
                // Large Cap
                //   [Yatin] 20/11/2019 MODIFIED - On click of tab the search will set to empty
                // this is done to ensure previous tabs search text does'nt affect the newly clicked tab
                //   [Yatin] 26/11/2019 MODIFIED - Removed the most active from state as MOST ACTIVE required volume data
                // which our database has only for about 16 companies, hence removed from our MVP
                onClick={() => {
                  this.setState({
                    large_cap: true,
                    most_volatile: false,
                    search_text: ""
                  });
                  this.props.getCompanies("largecap");
                }}
                //   [Yatin] 14/11/2019 CREATED - In the 'Market Mover' page when 'Large Cap' button is clicked then
                // css property will change with the text color as blue, this is done to show
                // the active selected tab on the page
                className={
                  this.state.large_cap === true &&
                  this.state.most_volatile === false
                    ? "onclick-active-color"
                    : null
                }
              >
                Large Cap
              </div>
              <div
                id="market-most-volatile"
                //   [Yatin] 14/11/2019 CREATED - In the 'Market Mover' page when 'Most Volatile' button is clicked then
                // the most_volatile state will be set to true, this is done to show the content of
                // Most Volatile
                //   [Yatin] 20/11/2019 MODIFIED - On click of tab the search will set to empty
                // this is done to ensure previous tabs search text does'nt affect the newly clicked tab
                //   [Yatin] 26/11/2019 MODIFIED - Removed the most active from state as MOST ACTIVE required volume data
                // which our database has only for about 16 companies, hence removed from our MVP
                onClick={() => {
                  this.setState({
                    large_cap: false,
                    most_volatile: true,
                    search_text: ""
                  });
                  this.props.getCompanies("volatile");
                }}
                //   [Yatin] 14/11/2019 CREATED - In the 'Market Mover' page when 'Most Volatile' button is clicked then
                // css property will change with the text color as blue, this is done to show
                // the active selected tab on the page
                className={
                  this.state.large_cap === false &&
                  this.state.most_volatile === true
                    ? "onclick-active-color"
                    : null
                }
              >
                Most Volatile
              </div>
              {/* [Yatin] 18/11/2019 CREATED - Search button feature component is implemented here */}
            </div>
            <div id="market-search" className="stocks-main-search-wrapper">
              <div className="stocks-search-container">
                <input
                  id="market-search-box-input"
                  name="search_text"
                  type="text"
                  className="stocks-search-input"
                  placeholder="Search Ticker Name"
                  value={this.state.search_text}
                  onChange={this.OnChange}
                />
                <button
                  id="market-search-button"
                  className="stocks-search-button"
                />
              </div>
            </div>
          </div>
          {this.props.isloading ? (
            <div
              className="loader-position"
              style={{
                height: 300 + "px",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <div>
                <Loader
                  type="Bars"
                  color="#15ddd6"
                  height="50px"
                  width="200px"
                />
                <span
                  style={{
                    marginTop: "40px",
                    color: "#15ddd6"
                  }}
                >
                  Loading Top 100 Companies Stocks...
                </span>
              </div>
            </div>
          ) : (
            <>
              {/* [Yatin] 14/11/2019 CREATED - Reusing the Table */}
              {/* // [Yatin] 15/11/2019 MODIFIED  When "LargeCap" or "Most Volatile" is clicked then page will display
              the content based on the selected tab */}
              {/*  [Yatin] 20/11/2019 MODIFIED - Checking the length of reducer state if length is 0 then
                 a message will be displayed saying 'No Result Found' */}
              {filteredCompanies.length > 0 ? (
                <Table
                  tableHeaders={[
                    "Ticker Name",
                    "Sector",
                    "Change",
                    "% Change",
                    "Market Cap (M)",
                    "Industry"
                  ]}
                  tableData={currentCompanies}
                  isIndex={false}
                />
              ) : (
                <div className="no-result-found"> No Result Found</div>
              )}
            </>
          )}
          {/* [Yatin] 25/11/2019 CREATED - When data is loading the pagination should not appear by default on the page */}
          {this.props.isloading ? null : (
            // {/* [Yatin] 24/11/2019 CREATED - [Pagination] When length is more than 11 rows then only pagination will apper*/}
            <>
              {filteredCompanies.length >= 11 ? (
                <>
                  <Pagination
                    companiesPerPage={this.state.companiesPerPage}
                    totalCompanies={filteredCompanies.length}
                    handlePageChange={this.handlePageChange}
                    currentCompanyPage={this.state.currentCompanyPage}
                  />
                </>
              ) : null}
            </>
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  companies: state.StocksCompanyReducer.companies,
  isloading: state.LoadingReducer.isLoading
});

export default connect(mapStateToProps, {
  getCompanies
})(MarketMovers);
