import {
  CREATE_USERS,
  SEND_OTP,
  OTP_VERIFICATION,
  LOGIN_USER,
  ERROR_TYPE,
  RESET_PASSWORD,
  CLEAR,
  FOLLOWED_TICKER_DETAILS,
  SEARCH_FOLLOWD_TICKER,
  ADVANCE_SEARCH,
  SUCCESS
} from "./Types";
import { startLoading, stopLoading } from "./LoaderAction";
import axios from "axios";
// Pranav: 16/11/19: All actions here are written to call server apis
const url = "https://stockit-server.herokuapp.com/api";
const Advance_search_url = "https://stockit-server.herokuapp.com/apis";
export const createUsers = (users, history) => dispatch => {
  return axios
    .post(url + "/users/new", users)
    .then(res => {
      dispatch({
        type: CREATE_USERS
      });
      dispatch(sendConfMail(users));
      history.push("/stocks-Login");
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
      // console.log(err.response.data);
    });
};

export const sendOtp = users => dispatch => {
  return (
    axios
      // .post("http://localhost:5000/api/users/sendotp", users)
      .post(url + "/users/sendotp", users)
      .then(res => {
        dispatch({
          type: SEND_OTP
        });
      })
      .catch(err => {
        dispatch({
          type: ERROR_TYPE,
          payload: err.response.data.message
        });
      })
  );
};

export const sendConfMail = users => dispatch => {
  return axios
    .post(url + "/users/comfirmation", users)
    .then(res => {
      dispatch({
        type: SEND_OTP
      });
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const downloadformat = () => dispatch => {
  return axios.get(url + "/users/download");
};

export const verifyOtp = (otp, user, history) => dispatch => {
  return axios
    .post(url + "/users/verifyotp", otp)
    .then(res => {
      dispatch({
        type: OTP_VERIFICATION
      });
      dispatch(createUsers(user, history));
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const verifyOtpForReset = (otp, user, history) => dispatch => {
  return axios
    .post(url + "/users/verifyotp", otp)
    .then(res => {
      dispatch({
        type: OTP_VERIFICATION
      });
      dispatch(resetPassword(user, history));
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const loginUser = (user, history) => dispatch => {
  // dispatch(startLoading());
  return axios
    .post(url + "/users/login", user)

    .then(res => {
      localStorage.setItem("token", res.data.token);
      // dispatch(stopLoading());

      dispatch({
        type: LOGIN_USER
      });
      history.push("/");
      window.location.reload();
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const resetPassword = (user, history) => dispatch => {
  // dispatch(startLoading());

  return axios
    .put(url + "/users/resetpassword", user)

    .then(res => {
      // dispatch(stopLoading());

      dispatch({
        type: RESET_PASSWORD
      });
      history.push("/stocks-Login");
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};
export const followedTickerDetails = id => dispatch => {
  dispatch(startLoading());

  return axios
    .get(url + "/users/followed-specific/details/" + id)

    .then(res => {
      dispatch(stopLoading());

      dispatch({
        type: FOLLOWED_TICKER_DETAILS,
        payload: res.data.TickerDetails
      });
    })
    .catch(err => {
      dispatch(stopLoading());

      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const followedTickerSearch = (id, ticker_name) => dispatch => {
  dispatch(startLoading());

  return axios
    .post(url + "/Users/followed-specific/search/" + id, ticker_name)

    .then(res => {
      dispatch(stopLoading());

      dispatch({
        type: SEARCH_FOLLOWD_TICKER,
        payload: res.data.TickerDetails
      });
    })
    .catch(err => {
      dispatch(stopLoading());

      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};

export const clearReducer = () => dispatch => {
  dispatch({
    type: CLEAR
  });
};

// Action for search recommendation -Rohan 25/11/19
export const advanceSearch = term => dispatch => {
  return axios
    .post(Advance_search_url + "/stockSearch/find-your-stock", term)
    .then(res => {
      dispatch({
        type: ADVANCE_SEARCH,
        payload: res.data.data
      });
    })
    .catch(err => {
      dispatch({
        type: ERROR_TYPE,
        payload: err.response.data.message
      });
    });
};
