import React, { Component } from "react";
import "../styles/RegisterUser.css";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { loginUser, clearReducer } from "../actions/User";

export class LoginUser extends Component {
  componentDidMount() {
    this.props.clearReducer();
  }

  state = {
    loginClicked: true,
    sendOTPClicked: false,
    email: "",
    password: "",
    conf_password: "",
    isAdmin: false
  };
  OnHandleChange = event => {
    const { value, name } = event.target;
    this.setState({
      [name]: value
    });
  };

  onRegisterClick = () => {
    this.props.history.push("/stocks-Register");
  };

  render() {
    return (
      <div className="Register-main-wrapper">
        <div className="login-box">
          <div className="lb-header">
            <button id="login-box-link1">LOGIN</button>
            <button id="signup-box-link" onClick={this.onRegisterClick}>
              SIGN UP
            </button>
          </div>
          <hr />
          <form className="email-login" novalidate>
            <div className="u-form-group  ">
              <input
                formnovalidate
                autocomplete="off"
                id="input-email-login"
                type="email"
                name="email"
                onChange={this.OnHandleChange}
                value={this.state.email}
                required
                minLength="8"
                maxLength="50"
              />

              {this.state.email !== "" ? (
                <label
                  style={{
                    transform: "translateY(-28px)"
                  }}
                  className="Placeholder-Register1"
                >
                  Email
                </label>
              ) : (
                <label className="Placeholder-Register1">Email</label>
              )}
            </div>

            <div className="u-form-group">
              <input
                formnovalidate
                autocomplete="off"
                id="input-login-password"
                name="password"
                type="password"
                value={this.state.password}
                onChange={this.OnHandleChange}
                minLength="3"
                maxLength="20"
                required
              />
              {this.state.password !== "" ? (
                <label
                  style={{
                    transform: "translateY(-28px)"
                  }}
                  className="Placeholder-Register2"
                >
                  Password
                </label>
              ) : (
                <label className="Placeholder-Register2">Password</label>
              )}
            </div>
            <div className="u-form-group" id="forget-pw">
              <Link to="/stocks-reset" className="forgot-password">
                Forgot password?
              </Link>

              <button
                id="login-button"
                type="button"
                className="Register-signup"
                onClick={() => {
                  let user = {
                    email: this.state.email,
                    password: this.state.password
                  };
                  this.props.loginUser(user, this.props.history);
                }}
              >
                LOGIN
              </button>
            </div>
          </form>
          <div id="server-error" className="show-error">
            {" "}
            {this.props.error}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  users: state.UserReducer.users,
  error: state.UserReducer.error,
  isloading: state.LoadingReducer.isLoading
});

export default connect(mapStateToProps, {
  loginUser,
  clearReducer
})(LoginUser);
