import React, { Component } from "react";
import "../styles/Navbar.css";
import { connect } from "react-redux";
import { navbarSearch } from "../actions/Search";
// import { getIndexName } from "../actions/Markets";
import { Link } from "react-router-dom";
import { debounce } from "lodash";
var stompClient = null;
const Stomp = require("stompjs");

var SockJS = require("sockjs-client");

export class Navbar extends Component {
  constructor(props) {
    super(props);
  }
  state = {
    searchTerm: "",
    market_selected: false,
    stock_selected: false,
    login_selected: false,
    profile_selected: false,
    screener_selected: false,
    data: []
  };
  removeToken = () => {
    // On click remove token item from the local storage -Rohan 17/11/19
    localStorage.removeItem("token");
    this.forceUpdate();
  };
  raiseSearchQuery = debounce(() => {
    // using debounce so that after the user has stopped typing it will wait for 1.5 seconds and then trigger the search -Rohan 17/11/19
    let searchTerm = {
      search_term: this.state.searchTerm
    };
    // Calling the action after 1.5 seconds -Rohan 17/11/19
    this.props.navbarSearch(searchTerm);
  }, 1500);
  userSearch = () => {
    // Call to the function when the user is typing -Rohan 16/11/19
    // Declaring the object to send to the action
    let searchObject = {
      search_term: this.state.searchTerm
    };
    // Call to the action to get reuslt -Rohan 16/11/19
    this.props.navbarSearch(searchObject);
  };
  OnChange = event => {
    this.setState({ [event.target.name]: event.target.value });
    // Call to the function when the user is typing -Rohan 16/11/19
    this.raiseSearchQuery();
  };

  onConnected = () => {
    this.setState({
      data: [],
      msg: "connected"
    });

    console.log("connected");

    // Subscribing to the public topic
    stompClient.subscribe("/topic/java", this.onMessageReceived);
  };

  onError = error => {
    console.log("error");

    this.setState({
      msg:
        "Could not connect you to the kafka Server. Please refresh this page and try again!"
    });
  };

  onMessageReceived = msg => {
    var body = JSON.parse(msg.body);
    // console.log(msg.body);
    this.setState({
      data: body
    });
    console.log(body);
  };

  componentDidMount() {
    // this.props.getIndexName();
    SockJS = new SockJS(
      "https://stockit-java-consumer-server.herokuapp.com/ws"
    );

    stompClient = Stomp.over(SockJS);

    stompClient.connect({}, this.onConnected, this.onError);
  }

  render() {
    return (
      <div className="sticky__container">
        <div className="index__flex-container" id="index-bar">
          {/* To be mapped */}
          {this.state.data.map((indexValue, index) => (
            <>
              {/* If the index value has a flag property true then change the color to red color showing negative impact --Rohan 17/11/19 */}
              {indexValue.flag == true ? (
                <div style={{ color: "var(--red-color)" }}>
                  <span id={"tickerName" + index}>
                    {" "}
                    {indexValue.ticker_name}
                  </span>
                  <div style={{ fontSize: "16px" }} id={"tickerInfo" + index}>
                    {" "}
                    {indexValue.closing.toFixed(2)} ({indexValue.percent_change}
                    %)
                  </div>
                </div>
              ) : (
                // If flag is false show green color i.e positive impact -Rohan 17/11/19
                <div style={{ color: "var(--green-color)" }}>
                  <span id={"tickerName" + index}>
                    {" "}
                    {indexValue.ticker_name}
                  </span>
                  <div style={{ fontSize: "16px" }} id={"tickerInfo" + index}>
                    {" "}
                    {indexValue.closing ? indexValue.closing.toFixed(2) : ""} (
                    {indexValue.percent_change ? indexValue.percent_change : ""}
                    %)
                  </div>
                </div>
              )}
            </>
          ))}
        </div>
        {/* Start of navbar -Rohan 11/11/19 */}
        <div className="navbar__container">
          <nav className="navbar__flex">
            <div className="navbar__left" id="Brand">
              <Link to="/">
                {" "}
                <span className="navbar__hamburger"> </span>
              </Link>

              <form className="navbar__search">
                <div className="navbar__inputwithicon">
                  {/* Checking if there is search term if not then not displaying the result division -Rohan 17/11/19 */}
                  {this.state.searchTerm === "" ? (
                    <input
                      id="searchTicker"
                      type="text"
                      name="searchTerm"
                      className="navbar__input"
                      placeholder="Search for ticker name"
                      value={this.state.searchTerm}
                      onChange={this.OnChange}
                    />
                  ) : (
                    <input
                      id="searchTicker"
                      type="text"
                      name="searchTerm"
                      className="navbar__input change-borderRadius"
                      placeholder="Search for ticker name"
                      value={this.state.searchTerm}
                      onChange={this.OnChange}
                    />
                  )}
                  <input
                    id="searchButton"
                    type="submit"
                    value=""
                    onClick={this.userSearch}
                    className="navbar__button"
                  />
                  {this.state.searchTerm === "" ? null : this.props
                      .search_result.length > 0 ? (
                    <ul className="search__result">
                      {/* Mapping the search reuslt -Rohan 12/11/19 */}
                      {this.props.search_result.map((result, index) => (
                        <li
                          id="click"
                          // Once selected setting state search term to blank so that result display is none again -Rohan 12/11/19
                          onClick={() => {
                            this.setState({
                              searchTerm: "",
                              market_selected: false,
                              login_selected: false,
                              screener_selected: false,
                              stock_selected: false
                            });
                          }}
                        >
                          {/* rendering the stocks component along with the current ticker id -Rohan 12/11/19  */}
                          <Link
                            to={{
                              pathname: "/stocks/" + result.ticker_id
                            }}
                          >
                            {/* If some data is missing displaying "NA" -Rohan 12/11/19 */}
                            <div className="resultItem">
                              <p id={"item" + index}>
                                {result.company_name
                                  ? result.company_name
                                  : "NA"}
                                ({"  "}
                                {result.ticker_name})
                              </p>
                              <p id={"industry" + index}>
                                Industry: {result.industry}
                              </p>
                            </div>
                          </Link>
                        </li>
                      ))}
                    </ul>
                  ) : (
                    // If the server throws an error then print that error else show loading screen -Rohan 12/11/19
                    <ul className="search__result">
                      {this.props.error ? (
                        <li
                          style={{ margin: "0px", padding: "10px 10px" }}
                          className="prompt"
                        >
                          {console.log(this.props.error)}
                          No Data Found
                        </li>
                      ) : (
                        <li
                          style={{ margin: "0px", padding: "10px 10px" }}
                          className="prompt"
                        >
                          Loading . . .
                        </li>
                      )}
                    </ul>
                  )}
                </div>
              </form>
            </div>
            <div className="navbar__right" id="Menu">
              <ul className="navbar__links">
                {this.state.market_selected == true ? (
                  <li className="underline">
                    <Link to="/" id="Market">
                      {" "}
                      Market
                    </Link>
                  </li>
                ) : (
                  <li
                    id="marketClick"
                    onClick={() => {
                      this.setState({
                        market_selected: true,
                        stock_selected: false,
                        profile_selected: false,
                        login_selected: false,
                        screener_selected: false
                      });
                    }}
                  >
                    <Link to="/" id="Market">
                      {" "}
                      Market
                    </Link>
                  </li>
                )}

                {this.state.stock_selected == true ? (
                  <li className="underline">
                    <Link to="/home/stocks" id="Stocks">
                      {" "}
                      Stocks
                    </Link>
                  </li>
                ) : (
                  <li
                    id="stockClick"
                    onClick={() => {
                      this.setState({
                        market_selected: false,
                        stock_selected: true,
                        profile_selected: false,
                        login_selected: false,
                        screener_selected: false
                      });
                    }}
                  >
                    <Link to="/home/stocks" id="Stocks">
                      {" "}
                      Stocks
                    </Link>
                  </li>
                )}
                {/* Only if there is a token present display Profile and Logout Links else display above list -Rohan 12/11/19 */}
                {localStorage.getItem("token") ? (
                  <>
                    {this.state.profile_selected == true ? (
                      <li className="underline">
                        <Link to="/stocks-user-profile" id="Profile">
                          Profile
                        </Link>
                      </li>
                    ) : (
                      <li
                        id="profileClick"
                        onClick={() => {
                          this.setState({
                            market_selected: false,
                            stock_selected: false,
                            profile_selected: true,
                            login_selected: false,
                            screener_selected: false
                          });
                        }}
                      >
                        <Link to="/stocks-user-profile" id="Profile">
                          Profile
                        </Link>
                      </li>
                    )}
                    {this.state.screener_selected == true ? (
                      <li className="underline">
                        <Link id="screener" to="/screener">
                          Screener
                        </Link>
                      </li>
                    ) : (
                      <li
                        onClick={() => {
                          this.setState({
                            market_selected: false,
                            stock_selected: false,
                            profile_selected: false,
                            login_selected: false,
                            screener_selected: true
                          });
                        }}
                      >
                        <Link id="screener" to="/screener">
                          Screener
                        </Link>
                      </li>
                    )}
                    <li>
                      <Link id="Logout" onClick={this.removeToken} to="/">
                        Logout
                      </Link>
                    </li>
                  </>
                ) : (
                  <>
                    {" "}
                    {this.state.login_selected == true ? (
                      <li id="Login" className="underline">
                        <Link to="/stocks-Login">Login</Link>
                      </li>
                    ) : (
                      <li
                        id="Login"
                        onClick={() => {
                          this.setState({
                            market_selected: false,
                            stock_selected: false,
                            profile_selected: false,
                            login_selected: true,
                            screener_selected: false
                          });
                        }}
                      >
                        <Link to="/stocks-Login">Login</Link>
                      </li>
                    )}
                  </>
                )}
              </ul>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  search_result: state.searchReducer.search_result,
  error: state.searchReducer.error
  // indexName: state.MarketsReducer.indexName
});
export default connect(mapStateToProps, { navbarSearch })(Navbar);
