import { SUCCESS, ERROR_TYPE } from "./Types";
import axios from "axios";
import { startLoading, stopLoading } from "./LoaderAction";

const url = "https://stockit-pranav.herokuapp.com/apis/admin";
// const url = "http://localhost:5000/apis/admin";

//[Pranav] action to upload file
export const upload = data => dispatch => {
  dispatch(startLoading());

  return axios
    .post(url + "/upload", data, {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`
      }
    })

    .then(res => {
      dispatch(stopLoading());

      dispatch({
        type: SUCCESS,
        payload: res.data.message
      });
    })
    .catch(err => {
      dispatch({ type: ERROR_TYPE, payload: err.response.data.message });
    });
};
