import React, { Component } from "react";
import "../styles/UserProfile.css";
import { connect } from "react-redux";
import "../styles/ReusableCards.css";

import { upload } from "../actions/upload";
import { clearReducer, downloadformat } from "../actions/User";
import "../styles/ReusableSearch.css";
import LinearProgress from "@material-ui/core/LinearProgress";

import UserNavigation from "./UserProfileNavigation";
export class AdminUpload extends Component {
  state = {
    selectedFile: "",
    files: []
  };

  onUpload = event => {
    this.props.clearReducer();
    const data = new FormData();
    this.setState({ selectedFile: event.target.files[0].name });
    data.append("file", event.target.files[0]);
    this.props.upload(data);
  };
  //pranav: 24/11/19: here onUpload function i am first clearing my reducer to initial state
  // then appending file data taken by user to const data and then calling upload action to upload data

  componentDidMount() {
    if (localStorage.getItem("isAdmin") === false) {
      this.props.history.push("/stocks-Login");
    }
  }
  render() {
    return (
      <div>
        <div className="main-user-profile-wrapper">
          <UserNavigation />
          <button
            className="download-format-button"
            onClick={() => {
              this.props.downloadformat();
            }}
          >
            Download Format
          </button>{" "}
          <input
            id="download-input-box"
            accept=".csv"
            type="file"
            className="input-tp-upload-file"
            onChange={this.onUpload}
          />
          <p className="upload-icon">+</p>
          <label id="admin-update-label" className="upload-label" for="file">
            Click or Drop to inset file
          </label>
          <p className="success-or-error-msg">{this.props.success}</p>
          <p className="success-or-error-msg1">{this.props.error}</p>
          {this.props.isloading ? (
            <div className="loader-wrapper">
              <div>
                <LinearProgress
                  className="load-progress-bar"
                  variant="determinate"
                />
                <span
                  style={{
                    marginTop: "40px",
                    color: "#15ddd6"
                  }}
                >
                  Uploading data...
                </span>
              </div>
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  isloading: state.LoadingReducer.isLoading,
  success: state.UserReducer.success,
  error: state.UserReducer.error
});
export default connect(mapStateToProps, {
  upload,
  clearReducer,
  downloadformat
})(AdminUpload);
