import React, { Component } from "react";
import "../styles/TickerTable.css";
import { exchangeIndexVis } from "../actions/Markets";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

export class TickerTable extends Component {
  render() {
    return (
      <div className="table__container">
        <table className="ticker__table">
          <thead className="ticker__tableHeader">
            <tr>
              {this.props.tableHeaders.map((data, index) => (
                // Mapping table headers -Rohan 16/11/19
                <th id={"header" + index}>{data}</th>
              ))}
            </tr>
          </thead>
          {this.props.tableData.map((current, index) => (
            // console.log("coming from ticker table",this.props.isIndex,current.ticker_name)
            <tr>
              {current.isIndex == true ? (
                // If isIndex is true then a function which generates graph is called -Rohan 16/11/19
                <td
                  id={"tickerName" + index}
                  onClick={() => {
                    this.props.exchangeIndexVis(current.ticker_name);
                  }}
                >
                  {/* If ticker name existst map else print "NA" -Rohan 16/11/19 */}
                  {current.ticker_name ? current.ticker_name : "NA"}
                </td>
              ) : !current.ticker_id ? (
                <td id={"tickerName" + index}>
                  {current.ticker_name ? current.ticker_name : "NA"}
                </td>
              ) : (
                // If is Index is False then provide link to stockspecific component with the ticker id -Rohan 16/11/19
                <Link to={{ pathname: "/stocks/" + current.ticker_id }}>
                  <td id={"tickerName" + index}>
                    {current.ticker_name ? current.ticker_name : "NA"}
                  </td>
                </Link>
              )}
              {/* Check if tickerValues exist -Rohan 16/11/19 */}
              {current.tickerValues
                ? Object.keys(current.tickerValues).map((keyName, index) => (
                    <>
                      {/* Check if the string has char - then change color to re -Rohan 16/11/19 */}
                      {current.tickerValues[keyName].substring(0, 1) == "-" ? (
                        <td
                          style={{ color: "var(--red-color)" }}
                          id={"data" + index}
                        >
                          {current.tickerValues[keyName]}
                        </td>
                      ) : // If string has + at first position then change color to green -Rohan 16/11/19
                      current.tickerValues[keyName].substring(0, 1) == "+" ? (
                        <td
                          style={{ color: "var(--green-color)" }}
                          id={"data" + index}
                        >
                          {current.tickerValues[keyName]}
                        </td>
                      ) : (
                        // If value dosen to contain + or - then color must be white -Rohan 16/11/19
                        <td id={"data" + index}>
                          {current.tickerValues[keyName]}
                        </td>
                      )}
                    </>
                  ))
                : null}
            </tr>
          ))}
        </table>
      </div>
    );
  }
}
// -Rohan 16/11/19
// Format :
// tableHeaders=["header1","header2","header3","header4"]
// isIndex=Boolean
// tableData=[
// {
//   ticker_id:1,
//   ticker_name:"name",
//   ticker_value:{
//    col1:"123",
//    col1:"123",
//    col1:"123",
//    col1:"123"
// }
// }
// ]
const mapStateToProps = state => ({});
// Connecting the call to action -Rohan 16/11/19
export default connect(mapStateToProps, { exchangeIndexVis })(TickerTable);
