import {
  CREATE_USERS,
  SEND_OTP,
  ERROR_TYPE,
  OTP_VERIFICATION,
  EMAIL_VERIFICATION,
  RESET_PASSWORD,
  CLEAR,
  FOLLOWED_TICKER_DETAILS,
  SEARCH_FOLLOWD_TICKER,
  ADVANCE_SEARCH,
  SUCCESS
} from "../actions/Types";

const initialstate = {
  users: [],
  error: "",
  success: "",
  search_result1: [],
  TickerDetails1: [],
  recommendationResult: []
};

export default function(state = initialstate, action) {
  switch (action.type) {
    case CREATE_USERS:
      return state;

    case SEND_OTP:
      return state;
    case EMAIL_VERIFICATION:
      return state;
    case RESET_PASSWORD:
      return state;
    case OTP_VERIFICATION:
      return state;
    case SEARCH_FOLLOWD_TICKER:
      return { ...state, search_result1: action.payload };

    case FOLLOWED_TICKER_DETAILS:
      return { ...state, TickerDetails1: action.payload };
    case ERROR_TYPE:
      return { ...state, error: action.payload };
    case SUCCESS:
      return { ...state, success: action.payload };
    case CLEAR:
      return initialstate;
    case ADVANCE_SEARCH:
      return { ...state, recommendationResult: action.payload, error: "" };
    default:
      return state;
  }
}
