import { SEARCH_BAR, ERROR } from "../actions/Types";
const initialState = {
  search_result: [],
  error: ""
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SEARCH_BAR:
      return { ...state, search_result: action.payload };
    case ERROR:
      return {
        ...state,
        error: action.payload
      };
    default:
      return state;
  }
}
