import { EXCHANGE_INDEX_VIS, INDEX_NAME, GAINERS, SECTORS } from "./Types";
import axios from "axios";
import { startLoading, stopLoading } from "./LoaderAction";
const url = "http://192.168.0.99:5000/";

export const exchangeIndexVis = indexRange => dispatch => {
  return axios
    .get(url + "exchange-index-vis/" + indexRange)
    .then(res => {
      dispatch({
        type: EXCHANGE_INDEX_VIS,
        payload: res.data
      });
      // console.log(res.data);
    })
    .catch(err => {
      // console.log(err);
    });
};

// Action to get index name and their data -Rohan 12/11/19
export const getIndexName = () => dispatch => {
  dispatch(startLoading());
  return axios
    .get("https://stockit-server.herokuapp.com/apis/Markets/index-name")
    .then(res => {
      dispatch(stopLoading());
      // If promise returns true call INDEX_NAME type and send data into payload to the client -Rohan 12/11/19
      dispatch({
        type: INDEX_NAME,
        payload: res.data.data
      });
    })
    .catch(err => {
      // console.log(err);
    });
};

export const getGainers = sectors => dispatch => {
  console.log("from gainers actions");
  dispatch(startLoading());
  return axios
    .get(
      "https://stockit-server.herokuapp.com/apis/Markets/gainers-and-losers/" +
        sectors
    )
    .then(res => {
      dispatch(stopLoading());
      dispatch({
        type: GAINERS,
        payload: res.data.data
      });
    })
    .catch(err => {
      // console.log(err);
    });
};

export const getSectors = () => dispatch => {
  return axios
    .get("https://stockit-server.herokuapp.com/apis/Markets/all-sectors")
    .then(res => {
      dispatch({
        type: SECTORS,
        payload: res.data.data
      });
      console.log("retrieved from sectors db", res.data.data);
    })
    .catch(err => {
      // consol27.0.0.1
    });
};
